import { useState, useEffect, useCallback } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import type { OfflineData, SyncStatus } from '../types';

export const useOfflineSync = () => {
  const { useStorage } = useSubscribeDev();
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [offlineData, setOfflineData] = useStorage<OfflineData>('offline_data', {
    lastSync: new Date(),
    cachedInsights: [],
    cachedMetrics: [],
    cachedTransactions: [],
    pendingChanges: [],
    isOffline: false,
  });

  const [syncStatus, setSyncStatus] = useState<SyncStatus>({
    isOnline: navigator.onLine,
    lastSync: offlineData.lastSync,
    pendingChanges: offlineData.pendingChanges.length,
    syncInProgress: false,
    conflicts: [],
  });

  // Listen for online/offline events
  useEffect(() => {
    const handleOnline = () => {
      setIsOnline(true);
      setSyncStatus(prev => ({ ...prev, isOnline: true }));
      syncPendingChanges();
    };

    const handleOffline = () => {
      setIsOnline(false);
      setSyncStatus(prev => ({ ...prev, isOnline: false }));
      setOfflineData({ ...offlineData, isOffline: true });
    };

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  const addPendingChange = useCallback((change: any) => {
    const newPendingChanges = [...offlineData.pendingChanges, { ...change, timestamp: Date.now() }];
    setOfflineData({
      ...offlineData,
      pendingChanges: newPendingChanges,
    });
    setSyncStatus(prev => ({ ...prev, pendingChanges: prev.pendingChanges + 1 }));
  }, [offlineData, setOfflineData]);

  const syncPendingChanges = useCallback(async () => {
    if (!isOnline || offlineData.pendingChanges.length === 0) return;

    setSyncStatus(prev => ({ ...prev, syncInProgress: true }));

    try {
      // Simulate sync process
      await new Promise(resolve => setTimeout(resolve, 2000));

      // Clear pending changes after successful sync
      setOfflineData({
        ...offlineData,
        pendingChanges: [],
        lastSync: new Date(),
        isOffline: false,
      });

      setSyncStatus(prev => ({
        ...prev,
        pendingChanges: 0,
        lastSync: new Date(),
        syncInProgress: false,
      }));
    } catch (error) {
      console.error('Sync failed:', error);
      setSyncStatus(prev => ({ ...prev, syncInProgress: false }));
    }
  }, [isOnline, offlineData, setOfflineData]);

  const cacheData = useCallback((type: string, data: any) => {
    setOfflineData({
      ...offlineData,
      [`cached${type}`]: data,
    });
  }, [offlineData, setOfflineData]);

  const getCachedData = useCallback((type: string) => {
    return offlineData[`cached${type}` as keyof OfflineData] || [];
  }, [offlineData]);

  const clearCache = useCallback(() => {
    setOfflineData({
      ...offlineData,
      cachedInsights: [],
      cachedMetrics: [],
      cachedTransactions: [],
    });
  }, [offlineData, setOfflineData]);

  return {
    isOnline,
    syncStatus,
    offlineData,
    addPendingChange,
    syncPendingChanges,
    cacheData,
    getCachedData,
    clearCache,
  };
};
