export interface EnvironmentConfig {
  subscribeDevApiKey: string;
  environment: 'development' | 'staging' | 'production';
  features: {
    enableAnalytics: boolean;
    enableErrorReporting: boolean;
    enableDebugMode: boolean;
    enableMockData: boolean;
    enablePerformanceMonitoring: boolean;
    enableSubscribeDev: boolean;
  };
  api: {
    baseUrl: string;
    timeout: number;
    retryAttempts: number;
  };
  security: {
    enableCSP: boolean;
    allowedOrigins: string[];
  };
}

export class EnvironmentError extends Error {
  public missingKeys: string[];
  public invalidKeys: string[];

  constructor(message: string, missingKeys: string[] = [], invalidKeys: string[] = []) {
    super(message);
    this.name = 'EnvironmentError';
    this.missingKeys = missingKeys;
    this.invalidKeys = invalidKeys;
  }
}

// Environment variable validators
const validateApiKey = (key: string): boolean => {
  // Very permissive validation - only check for non-empty string
  if (!key || typeof key !== 'string') return false;
  
  // Reject placeholder values
  if (key === 'dev-placeholder' || key === 'development-key' || key === 'your-api-key-here' || key === 'frontend-only-mode') {
    return false;
  }
  
  // For any environment, require a non-empty string that looks like a real API key
  return key.trim().length > 10 && (key.includes('_') || key.includes('-'));
};

const validateUrl = (url: string): boolean => {
  try {
    new URL(url);
    return true;
  } catch {
    return false;
  }
};

const validateEnvironmentType = (env: string): env is 'development' | 'staging' | 'production' => {
  return ['development', 'staging', 'production'].includes(env);
};

export const validateEnvironment = (): EnvironmentConfig => {
  const missingKeys: string[] = [];
  const invalidKeys: string[] = [];
  
  // Required environment variables
  const subscribeDevApiKey: string | undefined = import.meta.env.VITE_SUBSCRIBEDEV_PUBLIC_API_KEY;
  let enableSubscribeDev = false;
  
  // Check if API key is present and valid
  if (!subscribeDevApiKey) {
    console.warn('🔧 No Subscribe.dev API key provided (VITE_SUBSCRIBEDEV_PUBLIC_API_KEY), running in frontend-only mode');
    enableSubscribeDev = false;
  } else if (!validateApiKey(subscribeDevApiKey)) {
    console.warn('🔧 Invalid Subscribe.dev API key format, running in frontend-only mode');
    enableSubscribeDev = false;
  } else {
    console.log('✅ Subscribe.dev API key found, enabling full functionality');
    enableSubscribeDev = true;
  }

  // Optional environment variables with validation
  const apiBaseUrl = import.meta.env.VITE_API_BASE_URL || 'https://api.subscribe.dev';
  if (apiBaseUrl && !validateUrl(apiBaseUrl)) {
    invalidKeys.push('VITE_API_BASE_URL (invalid URL format)');
  }

  const environment = import.meta.env.MODE || 'development';
  if (!validateEnvironmentType(environment)) {
    invalidKeys.push('MODE (must be development, staging, or production)');
  }

  // Always provide a fallback API key for frontend-only mode
  const finalApiKey = enableSubscribeDev && subscribeDevApiKey ? subscribeDevApiKey : 'frontend-only-mode';

  // Never throw errors - always fall back to frontend-only mode
  if (missingKeys.length > 0 || invalidKeys.length > 0) {
    console.warn('Environment validation issues detected, falling back to frontend-only mode:', {
      missingKeys,
      invalidKeys
    });
    enableSubscribeDev = false;
  }

  // Parse optional configuration
  const enableAnalytics = import.meta.env.VITE_ENABLE_ANALYTICS === 'true';
  const enableErrorReporting = import.meta.env.VITE_ENABLE_ERROR_REPORTING === 'true';
  const enableMockData = import.meta.env.VITE_ENABLE_MOCK_DATA !== 'false'; // Default to true
  const enablePerformanceMonitoring = import.meta.env.VITE_ENABLE_PERFORMANCE_MONITORING === 'true';

  const apiTimeout = parseInt(import.meta.env.VITE_API_TIMEOUT || '10000', 10);
  const retryAttempts = parseInt(import.meta.env.VITE_RETRY_ATTEMPTS || '3', 10);

  const allowedOrigins = import.meta.env.VITE_ALLOWED_ORIGINS 
    ? import.meta.env.VITE_ALLOWED_ORIGINS.split(',').map(origin => origin.trim())
    : ['http://localhost:3000', 'http://localhost:5173'];

  return {
    subscribeDevApiKey: finalApiKey,
    environment: environment as 'development' | 'staging' | 'production',
    features: {
      enableAnalytics: environment === 'production' || enableAnalytics,
      enableErrorReporting: environment !== 'development' || enableErrorReporting,
      enableDebugMode: environment === 'development',
      enableMockData: environment === 'development' || enableMockData,
      enablePerformanceMonitoring: environment === 'production' || enablePerformanceMonitoring,
      enableSubscribeDev,
    },
    api: {
      baseUrl: apiBaseUrl,
      timeout: isNaN(apiTimeout) ? 10000 : apiTimeout,
      retryAttempts: isNaN(retryAttempts) ? 3 : retryAttempts,
    },
    security: {
      enableCSP: environment === 'production',
      allowedOrigins,
    },
  };
};

export const getEnvironmentConfig = (): EnvironmentConfig => {
  try {
    return validateEnvironment();
  } catch (error) {
    console.error('Environment validation failed:', error);
    
    // Always provide a fallback configuration instead of throwing
    console.warn('Using fallback development configuration (frontend-only mode)');
    return {
      subscribeDevApiKey: 'frontend-only-mode',
      environment: 'development',
      features: {
        enableAnalytics: false,
        enableErrorReporting: false,
        enableDebugMode: true,
        enableMockData: true,
        enablePerformanceMonitoring: false,
        enableSubscribeDev: false,
      },
      api: {
        baseUrl: 'https://api.subscribe.dev',
        timeout: 10000,
        retryAttempts: 3,
      },
      security: {
        enableCSP: false,
        allowedOrigins: ['http://localhost:3000', 'http://localhost:5173'],
      },
    };
  }
};

// Environment-specific utilities
export const isDevelopment = () => import.meta.env.DEV;
export const isProduction = () => import.meta.env.PROD;
export const isStaging = () => import.meta.env.MODE === 'staging';

// Feature flags based on environment
export const getFeatureFlags = (config: EnvironmentConfig) => ({
  showDebugInfo: config.features.enableDebugMode,
  enableMockData: config.features.enableMockData,
  enableAnalytics: config.features.enableAnalytics,
  enableErrorReporting: config.features.enableErrorReporting,
  enablePerformanceMonitoring: config.features.enablePerformanceMonitoring,
  enableSubscribeDev: config.features.enableSubscribeDev,
  strictValidation: config.environment === 'production',
});

// API configuration helper
export const getApiConfig = (config: EnvironmentConfig) => ({
  baseURL: config.api.baseUrl,
  timeout: config.api.timeout,
  retryAttempts: config.api.retryAttempts,
  headers: {
    'Content-Type': 'application/json',
    'X-Environment': config.environment,
  },
});

// Security configuration helper
export const getSecurityConfig = (config: EnvironmentConfig) => ({
  enableCSP: config.security.enableCSP,
  allowedOrigins: config.security.allowedOrigins,
  strictMode: config.environment === 'production',
});
