import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { TrendingUp, Brain, BarChart3, Lightbulb, RefreshCw, ChevronRight, Calendar, Target } from 'lucide-react';
import { usePredictiveAnalytics } from '../hooks/usePredictiveAnalytics';
import TypewriterText from './TypewriterText';

const PredictiveInsights: React.FC = () => {
  const { insights, correlations, isAnalyzing, generatePredictiveInsights, generateCrossDomainCorrelations } = usePredictiveAnalytics();
  const [activeTab, setActiveTab] = useState<'insights' | 'correlations'>('insights');
  const [expandedInsight, setExpandedInsight] = useState<string | null>(null);

  const getInsightIcon = (type: string) => {
    switch (type) {
      case 'productivity': return Target;
      case 'health': return TrendingUp;
      case 'financial': return BarChart3;
      case 'communication': return Brain;
      default: return Lightbulb;
    }
  };

  const getInsightColor = (type: string) => {
    switch (type) {
      case 'productivity': return 'from-blue-500 to-indigo-600';
      case 'health': return 'from-red-500 to-pink-600';
      case 'financial': return 'from-green-500 to-emerald-600';
      case 'communication': return 'from-purple-500 to-violet-600';
      default: return 'from-gray-500 to-gray-600';
    }
  };

  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 80) return 'text-green-500';
    if (confidence >= 60) return 'text-yellow-500';
    return 'text-red-500';
  };

  const getCorrelationStrength = (correlation: number) => {
    const abs = Math.abs(correlation);
    if (abs >= 0.7) return { label: 'Strong', color: 'text-green-500' };
    if (abs >= 0.4) return { label: 'Moderate', color: 'text-yellow-500' };
    return { label: 'Weak', color: 'text-red-500' };
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white">Predictive Analytics</h2>
          <p className="text-gray-600 dark:text-gray-400">AI-powered insights based on your patterns</p>
        </div>
        <motion.button
          onClick={() => {
            generatePredictiveInsights();
            generateCrossDomainCorrelations();
          }}
          disabled={isAnalyzing}
          className="btn-primary flex items-center gap-2"
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          <RefreshCw className={`w-4 h-4 ${isAnalyzing ? 'animate-spin' : ''}`} />
          {isAnalyzing ? 'Analyzing...' : 'Refresh Analysis'}
        </motion.button>
      </div>

      {/* Tab Navigation */}
      <div className="flex gap-2">
        <motion.button
          onClick={() => setActiveTab('insights')}
          className={`px-4 py-2 rounded-lg font-medium transition-colors ${
            activeTab === 'insights'
              ? 'bg-purple-500 text-white'
              : 'text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-800'
          }`}
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          <div className="flex items-center gap-2">
            <Lightbulb className="w-4 h-4" />
            Predictive Insights
          </div>
        </motion.button>
        <motion.button
          onClick={() => setActiveTab('correlations')}
          className={`px-4 py-2 rounded-lg font-medium transition-colors ${
            activeTab === 'correlations'
              ? 'bg-purple-500 text-white'
              : 'text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-800'
          }`}
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          <div className="flex items-center gap-2">
            <BarChart3 className="w-4 h-4" />
            Cross-Domain Analysis
          </div>
        </motion.button>
      </div>

      <AnimatePresence mode="wait">
        {activeTab === 'insights' && (
          <motion.div
            key="insights"
            className="space-y-4"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.3 }}
          >
            {insights.length === 0 ? (
              <div className="glass-card p-8 text-center">
                <Brain className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
                  No Predictive Insights Yet
                </h3>
                <p className="text-gray-600 dark:text-gray-400 mb-4">
                  Generate insights based on your historical data patterns
                </p>
                <motion.button
                  onClick={generatePredictiveInsights}
                  disabled={isAnalyzing}
                  className="btn-primary"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  Generate First Insights
                </motion.button>
              </div>
            ) : (
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {insights.map((insight, index) => {
                  const Icon = getInsightIcon(insight.type);
                  const colorClass = getInsightColor(insight.type);
                  const isExpanded = expandedInsight === insight.id;

                  return (
                    <motion.div
                      key={insight.id}
                      className="glass-card p-6 cursor-pointer hover:shadow-lg transition-all duration-300"
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: index * 0.1 }}
                      whileHover={{ scale: 1.02 }}
                      onClick={() => setExpandedInsight(isExpanded ? null : insight.id)}
                    >
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex items-center gap-3">
                          <div className={`w-10 h-10 rounded-lg bg-gradient-to-br ${colorClass} flex items-center justify-center`}>
                            <Icon className="w-5 h-5 text-white" />
                          </div>
                          <div>
                            <h3 className="font-semibold text-gray-900 dark:text-white capitalize">
                              {insight.type} Prediction
                            </h3>
                            <div className="flex items-center gap-2 mt-1">
                              <Calendar className="w-3 h-3 text-gray-400" />
                              <span className="text-xs text-gray-500 dark:text-gray-400">
                                {insight.timeframe}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          <div className={`text-sm font-bold ${getConfidenceColor(insight.confidence)}`}>
                            {insight.confidence}%
                          </div>
                          <ChevronRight className={`w-4 h-4 text-gray-400 transition-transform ${isExpanded ? 'rotate-90' : ''}`} />
                        </div>
                      </div>

                      <div className="mb-4">
                        <TypewriterText 
                          text={insight.prediction} 
                          speed={20}
                          className="text-gray-700 dark:text-gray-300 leading-relaxed"
                        />
                      </div>

                      <AnimatePresence>
                        {isExpanded && (
                          <motion.div
                            className="border-t border-gray-200 dark:border-gray-700 pt-4"
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: 'auto' }}
                            exit={{ opacity: 0, height: 0 }}
                            transition={{ duration: 0.3 }}
                          >
                            <h4 className="font-semibold text-gray-900 dark:text-white mb-2">
                              Based On:
                            </h4>
                            <ul className="space-y-1">
                              {insight.basedOn.map((factor, idx) => (
                                <li key={idx} className="text-sm text-gray-600 dark:text-gray-400 flex items-center gap-2">
                                  <div className="w-1.5 h-1.5 bg-purple-500 rounded-full"></div>
                                  {factor}
                                </li>
                              ))}
                            </ul>
                            
                            {insight.actionable && (
                              <div className="mt-3 p-3 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg">
                                <p className="text-sm text-blue-700 dark:text-blue-300 font-medium">
                                  💡 This insight includes actionable recommendations
                                </p>
                              </div>
                            )}
                          </motion.div>
                        )}
                      </AnimatePresence>
                    </motion.div>
                  );
                })}
              </div>
            )}
          </motion.div>
        )}

        {activeTab === 'correlations' && (
          <motion.div
            key="correlations"
            className="space-y-4"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.3 }}
          >
            {correlations.length === 0 ? (
              <div className="glass-card p-8 text-center">
                <BarChart3 className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
                  No Correlations Found Yet
                </h3>
                <p className="text-gray-600 dark:text-gray-400 mb-4">
                  Analyze relationships between different aspects of your life
                </p>
                <motion.button
                  onClick={generateCrossDomainCorrelations}
                  disabled={isAnalyzing}
                  className="btn-primary"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  Analyze Correlations
                </motion.button>
              </div>
            ) : (
              <div className="space-y-4">
                {correlations.map((correlation, index) => {
                  const strength = getCorrelationStrength(correlation.correlation);
                  
                  return (
                    <motion.div
                      key={correlation.id}
                      className="glass-card p-6"
                      initial={{ opacity: 0, x: -20 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: index * 0.1 }}
                    >
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex-1">
                          <div className="flex items-center gap-3 mb-2">
                            <div className="flex items-center gap-2">
                              {correlation.domains.map((domain, idx) => (
                                <React.Fragment key={domain}>
                                  <span className="px-2 py-1 bg-purple-100 dark:bg-purple-900/30 text-purple-600 dark:text-purple-400 rounded text-sm font-medium capitalize">
                                    {domain}
                                  </span>
                                  {idx < correlation.domains.length - 1 && (
                                    <span className="text-gray-400">↔</span>
                                  )}
                                </React.Fragment>
                              ))}
                            </div>
                            <div className={`px-2 py-1 rounded text-xs font-bold ${strength.color} bg-gray-100 dark:bg-gray-800`}>
                              {strength.label}
                            </div>
                          </div>
                          <h3 className="font-semibold text-gray-900 dark:text-white mb-2">
                            Correlation Strength: {(correlation.correlation * 100).toFixed(0)}%
                          </h3>
                          <p className="text-gray-600 dark:text-gray-400 leading-relaxed">
                            {correlation.insight}
                          </p>
                        </div>
                      </div>

                      <div className="flex items-center justify-between text-sm text-gray-500 dark:text-gray-400">
                        <span>Based on {correlation.dataPoints} data points</span>
                        <span>{correlation.timeframe}</span>
                      </div>

                      {/* Correlation Visualization */}
                      <div className="mt-4 p-3 bg-gray-50 dark:bg-gray-800/50 rounded-lg">
                        <div className="flex items-center justify-between mb-2">
                          <span className="text-xs font-medium text-gray-600 dark:text-gray-400">
                            Correlation Strength
                          </span>
                          <span className={`text-xs font-bold ${strength.color}`}>
                            {Math.abs(correlation.correlation).toFixed(2)}
                          </span>
                        </div>
                        <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                          <motion.div
                            className={`h-2 rounded-full ${
                              correlation.correlation > 0 
                                ? 'bg-gradient-to-r from-green-400 to-green-600' 
                                : 'bg-gradient-to-r from-red-400 to-red-600'
                            }`}
                            initial={{ width: 0 }}
                            animate={{ width: `${Math.abs(correlation.correlation) * 100}%` }}
                            transition={{ duration: 1, delay: index * 0.2 }}
                          />
                        </div>
                      </div>
                    </motion.div>
                  );
                })}
              </div>
            )}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default PredictiveInsights;
