import React, { useState, useEffect } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { Heart, Activity, Moon, Footprints, Target, Loader } from 'lucide-react';
import type { HealthMetric, HealthScore } from '../types';
import TypewriterText from './TypewriterText';
import RadialProgress from './RadialProgress';
import ErrorFallback from './ErrorFallback';

const HealthDashboard: React.FC = () => {
  const { client, useStorage, subscriptionStatus } = useSubscribeDev();
  
  const [healthScore] = useStorage<HealthScore>('health_score', {
    overall: 78,
    sleep: 85,
    activity: 72,
    nutrition: 76,
    lastUpdated: new Date()
  });

  const [healthMetrics] = useStorage<HealthMetric[]>('health_metrics', [
    { id: '1', type: 'steps', value: 8420, unit: 'steps', date: new Date(), source: 'iPhone' },
    { id: '2', type: 'sleep', value: 7.2, unit: 'hours', date: new Date(), source: 'Apple Watch' },
    { id: '3', type: 'heartRate', value: 68, unit: 'bpm', date: new Date(), source: 'Apple Watch' },
    { id: '4', type: 'exercise', value: 45, unit: 'minutes', date: new Date(), source: 'Apple Watch' }
  ]);

  const [aiInsight, setAiInsight] = useState<string>('');
  const [isLoadingInsight, setIsLoadingInsight] = useState(false);
  const [insightError, setInsightError] = useState<Error | null>(null);

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;

  const generateHealthInsight = async () => {
    if (!client || !isPremiumUser) return;

    setIsLoadingInsight(true);
    setInsightError(null);
    try {
      const stepsMetric = healthMetrics.find(m => m.type === 'steps');
      const sleepMetric = healthMetrics.find(m => m.type === 'sleep');
      const exerciseMetric = healthMetrics.find(m => m.type === 'exercise');

      const response = await client.run('openai/gpt-4o', {
        input: {
          messages: [{
            role: 'user',
            content: `As a health coach AI, analyze this health data and provide a brief, actionable insight:
            
            Overall Health Score: ${healthScore.overall}%
            Steps Today: ${stepsMetric?.value || 0}
            Sleep Last Night: ${sleepMetric?.value || 0} hours
            Exercise Today: ${exerciseMetric?.value || 0} minutes
            
            Provide one specific health recommendation in 1-2 sentences to improve their wellness.`
          }]
        }
      });

      setAiInsight(response.output[0] as string);
    } catch (error) {
      setInsightError(error as Error);
    } finally {
      setIsLoadingInsight(false);
    }
  };

  useEffect(() => {
    if (isPremiumUser) {
      generateHealthInsight();
    }
  }, [isPremiumUser]);

  const getMetricIcon = (type: string) => {
    switch (type) {
      case 'steps': return Footprints;
      case 'sleep': return Moon;
      case 'heartRate': return Heart;
      case 'exercise': return Activity;
      default: return Target;
    }
  };

  const getMetricColor = (type: string) => {
    switch (type) {
      case 'steps': return 'text-blue-500';
      case 'sleep': return 'text-purple-500';
      case 'heartRate': return 'text-red-500';
      case 'exercise': return 'text-green-500';
      default: return 'text-gray-500';
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-white">Health Overview</h2>
        <div className="flex items-center gap-2 text-sm text-gray-500 dark:text-gray-400">
          <Heart className="w-4 h-4" />
          <span>Last updated: {healthScore.lastUpdated.toLocaleDateString()}</span>
        </div>
      </div>

      {/* Health Score Overview */}
      <div className="bg-white dark:bg-gray-800/50 border border-gray-200 dark:border-gray-700 rounded-xl p-6">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-6">Health Score</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
          <RadialProgress 
            percentage={healthScore.overall} 
            colorClass="text-purple-500" 
            Icon={Target} 
            label="Overall"
          />
          <RadialProgress 
            percentage={healthScore.sleep} 
            colorClass="text-blue-500" 
            Icon={Moon} 
            label="Sleep"
          />
          <RadialProgress 
            percentage={healthScore.activity} 
            colorClass="text-green-500" 
            Icon={Activity} 
            label="Activity"
          />
          <RadialProgress 
            percentage={healthScore.nutrition} 
            colorClass="text-orange-500" 
            Icon={Heart} 
            label="Nutrition"
          />
        </div>
      </div>

      {/* Today's Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        {healthMetrics.map((metric) => {
          const Icon = getMetricIcon(metric.type);
          const colorClass = getMetricColor(metric.type);
          
          return (
            <div key={metric.id} className="bg-white dark:bg-gray-800/50 border border-gray-200 dark:border-gray-700 rounded-xl p-6 transition-all duration-300 hover:shadow-lg">
              <div className="flex items-center justify-between mb-2">
                <Icon className={`w-6 h-6 ${colorClass}`} />
                <span className="text-xs text-gray-500 dark:text-gray-400">{metric.source}</span>
              </div>
              <h3 className="font-semibold text-gray-900 dark:text-white capitalize mb-1">
                {metric.type === 'heartRate' ? 'Heart Rate' : metric.type}
              </h3>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">
                {metric.value.toLocaleString()}
                <span className="text-sm font-normal text-gray-500 dark:text-gray-400 ml-1">
                  {metric.unit}
                </span>
              </p>
            </div>
          );
        })}
      </div>

      {/* AI Health Insight */}
      <div className="bg-gradient-to-br from-red-50 to-pink-50 dark:from-red-900/20 dark:to-pink-900/20 border border-red-200 dark:border-red-700/50 rounded-xl p-6">
        <div className="flex items-center gap-2 mb-3">
          <Heart className="w-5 h-5 text-red-600 dark:text-red-400" />
          <h3 className="font-semibold text-red-800 dark:text-red-300">AI Health Insight</h3>
        </div>
        {!isPremiumUser ? (
          <p className="text-red-700 dark:text-red-400">
            Upgrade to Premium for personalized health insights and wellness recommendations.
          </p>
        ) : isLoadingInsight ? (
          <div className="flex items-center gap-2 text-red-700 dark:text-red-400">
            <Loader className="w-4 h-4 animate-spin" />
            <span>Analyzing your health data...</span>
          </div>
        ) : insightError ? (
          <ErrorFallback
            error={insightError}
            resetError={generateHealthInsight}
            title="Insight Error"
            description="Could not generate health insight."
            variant="embedded"
          />
        ) : aiInsight ? (
          <div className="text-red-800 dark:text-red-300">
            <TypewriterText text={aiInsight} speed={25} />
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default HealthDashboard;
