import React, { Component } from 'react';
import type { ReactNode } from 'react';
import { AlertTriangle, RefreshCw, Home, Mail, Wifi, WifiOff } from 'lucide-react';
import { motion } from 'framer-motion';

interface Props {
  children: ReactNode;
  fallback?: ReactNode;
  onError?: (error: Error, errorInfo: React.ErrorInfo) => void;
}

interface State {
  hasError: boolean;
  error: Error | null;
  errorInfo: React.ErrorInfo | null;
  isAuthError: boolean;
  isNetworkError: boolean;
  retryCount: number;
  suppressAuthErrors: boolean;
}

export class ErrorBoundary extends Component<Props, State> {
  private maxRetries = 1; // Reduced from 3 to fail faster
  private authErrorTimeout: NodeJS.Timeout | null = null;

  constructor(props: Props) {
    super(props);
    this.state = {
      hasError: false,
      error: null,
      errorInfo: null,
      isAuthError: false,
      isNetworkError: false,
      retryCount: 0,
      suppressAuthErrors: false,
    };
  }

  static getDerivedStateFromError(error: Error): Partial<State> {
    const isAuthError = error.message?.includes('Invalid Credentials') || 
                       error.message?.includes('SDE008') ||
                       error.message?.includes('Authentication') ||
                       error.name === 'AuthenticationError';
    
    const isNetworkError = error.message?.includes('Network') ||
                          error.message?.includes('fetch') ||
                          error.name === 'NetworkError';

    return {
      hasError: true,
      error,
      errorInfo: null,
      isAuthError,
      isNetworkError,
    };
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    this.setState({
      error,
      errorInfo,
    });

    // For authentication errors, suppress repeated errors for a period
    if (this.state.isAuthError) {
      if (!this.state.suppressAuthErrors) {
        console.warn('🔧 Subscribe.dev authentication error caught by boundary - switching to frontend-only mode');
        this.setState({ suppressAuthErrors: true });
        
        // Clear suppression after 30 seconds
        this.authErrorTimeout = setTimeout(() => {
          this.setState({ suppressAuthErrors: false });
        }, 30000);
      }
      
      // Immediately trigger parent error handler for auth errors
      if (this.props.onError) {
        this.props.onError(error, errorInfo);
      }
      return;
    }

    if (this.props.onError) {
      this.props.onError(error, errorInfo);
    }

    // Log to console in development
    if (import.meta.env.DEV) {
      console.error('Error caught by boundary:', error, errorInfo);
    }
  }

  componentWillUnmount() {
    if (this.authErrorTimeout) {
      clearTimeout(this.authErrorTimeout);
    }
  }

  handleRetry = () => {
    const newRetryCount = this.state.retryCount + 1;
    
    // If we've exceeded max retries for auth errors, trigger parent error handler
    if (this.state.isAuthError && newRetryCount >= this.maxRetries) {
      console.warn('Max retries exceeded for authentication, triggering fallback mode');
      if (this.props.onError && this.state.error) {
        this.props.onError(this.state.error, this.state.errorInfo || { componentStack: '' });
      }
      return;
    }
    
    this.setState({
      hasError: false,
      error: null,
      errorInfo: null,
      isAuthError: false,
      isNetworkError: false,
      retryCount: newRetryCount,
    });
  };

  handleReload = () => {
    window.location.reload();
  };

  render() {
    if (this.state.hasError) {
      if (this.props.fallback) {
        return this.props.fallback;
      }

      // Special handling for authentication errors - don't show UI, let parent handle
      if (this.state.isAuthError) {
        // If we're suppressing auth errors, don't show anything
        if (this.state.suppressAuthErrors) {
          return null;
        }

        return (
          <div className="min-h-screen bg-amber-50 dark:bg-amber-950 flex items-center justify-center p-4">
            <motion.div 
              className="max-w-md w-full glass-card p-8 text-center border-amber-200 dark:border-amber-800"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <motion.div 
                className="w-16 h-16 bg-amber-100 dark:bg-amber-900/30 rounded-2xl flex items-center justify-center mx-auto mb-6"
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.2, type: "spring", stiffness: 500, damping: 15 }}
              >
                <WifiOff className="w-8 h-8 text-amber-500" />
              </motion.div>
              
              <h1 className="text-2xl font-bold text-amber-900 dark:text-amber-100 mb-3">
                Service Configuration Issue
              </h1>
              
              <p className="text-amber-700 dark:text-amber-300 mb-6 leading-relaxed">
                The AI service configuration needs attention. Switching to demo mode with limited functionality.
              </p>

              <div className="space-y-3">
                <motion.button
                  onClick={this.handleReload}
                  className="w-full bg-amber-600 hover:bg-amber-700 text-white font-medium py-3 px-4 rounded-lg transition-colors flex items-center justify-center gap-2"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <Home className="w-4 h-4" />
                  Continue in Demo Mode
                </motion.button>
              </div>

              <div className="mt-6 p-4 bg-amber-100 dark:bg-amber-900/20 rounded-lg">
                <p className="text-xs text-amber-700 dark:text-amber-400">
                  To enable full functionality, configure your Subscribe.dev API key in the environment variables.
                </p>
              </div>
            </motion.div>
          </div>
        );
      }

      // Special handling for network errors
      if (this.state.isNetworkError) {
        return (
          <div className="min-h-screen bg-blue-50 dark:bg-blue-950 flex items-center justify-center p-4">
            <motion.div 
              className="max-w-md w-full glass-card p-8 text-center border-blue-200 dark:border-blue-800"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <motion.div 
                className="w-16 h-16 bg-blue-100 dark:bg-blue-900/30 rounded-2xl flex items-center justify-center mx-auto mb-6"
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.2, type: "spring", stiffness: 500, damping: 15 }}
              >
                <Wifi className="w-8 h-8 text-blue-500" />
              </motion.div>
              
              <h1 className="text-2xl font-bold text-blue-900 dark:text-blue-100 mb-3">
                Connection Error
              </h1>
              
              <p className="text-blue-700 dark:text-blue-300 mb-6 leading-relaxed">
                Unable to connect to our servers. Please check your internet connection and try again.
              </p>

              <div className="space-y-3">
                <motion.button
                  onClick={this.handleRetry}
                  className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-3 px-4 rounded-lg transition-colors flex items-center justify-center gap-2"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <RefreshCw className="w-4 h-4" />
                  Retry Connection
                </motion.button>
              </div>
            </motion.div>
          </div>
        );
      }

      // Default error handling
      return (
        <div className="min-h-screen bg-gray-50 dark:bg-gray-950 flex items-center justify-center p-4">
          <motion.div 
            className="max-w-md w-full glass-card p-8 text-center"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <motion.div 
              className="w-16 h-16 bg-red-100 dark:bg-red-900/30 rounded-2xl flex items-center justify-center mx-auto mb-6"
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ delay: 0.2, type: "spring", stiffness: 500, damping: 15 }}
            >
              <AlertTriangle className="w-8 h-8 text-red-500" />
            </motion.div>
            
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-3">
              Something went wrong
            </h1>
            
            <p className="text-gray-600 dark:text-gray-400 mb-6 leading-relaxed">
              We encountered an unexpected error. This has been logged and our team will investigate.
            </p>

            {import.meta.env.DEV && this.state.error && (
              <motion.div 
                className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4 mb-6 text-left"
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: 'auto' }}
                transition={{ delay: 0.3 }}
              >
                <h3 className="text-sm font-semibold text-red-800 dark:text-red-200 mb-2">
                  Error Details (Development Mode)
                </h3>
                <pre className="text-xs text-red-700 dark:text-red-300 overflow-auto max-h-32">
                  {this.state.error.message}
                </pre>
              </motion.div>
            )}

            <div className="space-y-3">
              <motion.button
                onClick={this.handleRetry}
                className="w-full btn-primary flex items-center justify-center gap-2"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <RefreshCw className="w-4 h-4" />
                Try Again
              </motion.button>
              
              <motion.button
                onClick={this.handleReload}
                className="w-full btn-secondary flex items-center justify-center gap-2"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <Home className="w-4 h-4" />
                Reload Page
              </motion.button>
            </div>

            <div className="mt-8 pt-6 border-t border-gray-200 dark:border-gray-700">
              <p className="text-xs text-gray-500 dark:text-gray-400 mb-3">
                Need help? Contact our support team
              </p>
              <a 
                href="mailto:support@aura.ai" 
                className="inline-flex items-center gap-2 text-sm text-purple-600 dark:text-purple-400 hover:text-purple-700 dark:hover:text-purple-300 transition-colors"
              >
                <Mail className="w-4 h-4" />
                support@aura.ai
              </a>
            </div>
          </motion.div>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;
