import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Wifi, WifiOff, Cloud, CloudOff, RefreshCw, AlertCircle, CheckCircle, Clock } from 'lucide-react';
import { useOfflineSync } from '../hooks/useOfflineSync';

const OfflineIndicator: React.FC = () => {
  const { isOnline, syncStatus, syncPendingChanges } = useOfflineSync();

  const getStatusIcon = () => {
    if (!isOnline) return WifiOff;
    if (syncStatus.syncInProgress) return RefreshCw;
    if (syncStatus.pendingChanges > 0) return Clock;
    if (syncStatus.conflicts.length > 0) return AlertCircle;
    return CheckCircle;
  };

  const getStatusColor = () => {
    if (!isOnline) return 'text-red-500';
    if (syncStatus.syncInProgress) return 'text-blue-500';
    if (syncStatus.pendingChanges > 0) return 'text-yellow-500';
    if (syncStatus.conflicts.length > 0) return 'text-orange-500';
    return 'text-green-500';
  };

  const getStatusText = () => {
    if (!isOnline) return 'Offline';
    if (syncStatus.syncInProgress) return 'Syncing...';
    if (syncStatus.pendingChanges > 0) return `${syncStatus.pendingChanges} pending`;
    if (syncStatus.conflicts.length > 0) return 'Conflicts detected';
    return 'Synced';
  };

  const StatusIcon = getStatusIcon();

  return (
    <AnimatePresence>
      <motion.div
        className="fixed top-4 right-4 z-50"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: -20 }}
      >
        <motion.div
          className={`glass-card px-4 py-2 flex items-center gap-3 ${
            !isOnline ? 'border-red-200 dark:border-red-800 bg-red-50 dark:bg-red-900/20' :
            syncStatus.pendingChanges > 0 ? 'border-yellow-200 dark:border-yellow-800 bg-yellow-50 dark:bg-yellow-900/20' :
            'border-green-200 dark:border-green-800 bg-green-50 dark:bg-green-900/20'
          }`}
          whileHover={{ scale: 1.02 }}
          layout
        >
          <motion.div
            animate={syncStatus.syncInProgress ? { rotate: 360 } : {}}
            transition={syncStatus.syncInProgress ? { duration: 1, repeat: Infinity, ease: "linear" } : {}}
          >
            <StatusIcon className={`w-4 h-4 ${getStatusColor()}`} />
          </motion.div>
          
          <div className="flex items-center gap-2">
            <span className={`text-sm font-medium ${getStatusColor()}`}>
              {getStatusText()}
            </span>
            
            {syncStatus.pendingChanges > 0 && isOnline && (
              <motion.button
                onClick={syncPendingChanges}
                className="text-xs px-2 py-1 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                Sync Now
              </motion.button>
            )}
          </div>
        </motion.div>

        {/* Detailed Status Panel */}
        <AnimatePresence>
          {(!isOnline || syncStatus.pendingChanges > 0 || syncStatus.conflicts.length > 0) && (
            <motion.div
              className="mt-2 glass-card p-4 w-80"
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
            >
              <div className="space-y-3">
                <div className="flex items-center gap-2">
                  {isOnline ? (
                    <Wifi className="w-4 h-4 text-green-500" />
                  ) : (
                    <WifiOff className="w-4 h-4 text-red-500" />
                  )}
                  <span className="text-sm font-medium text-gray-900 dark:text-white">
                    {isOnline ? 'Connected' : 'No Internet Connection'}
                  </span>
                </div>

                <div className="flex items-center gap-2">
                  {syncStatus.pendingChanges === 0 ? (
                    <Cloud className="w-4 h-4 text-green-500" />
                  ) : (
                    <CloudOff className="w-4 h-4 text-yellow-500" />
                  )}
                  <span className="text-sm text-gray-600 dark:text-gray-400">
                    {syncStatus.pendingChanges === 0 
                      ? 'All changes synced' 
                      : `${syncStatus.pendingChanges} changes pending sync`
                    }
                  </span>
                </div>

                {syncStatus.lastSync && (
                  <div className="text-xs text-gray-500 dark:text-gray-400">
                    Last sync: {syncStatus.lastSync.toLocaleTimeString()}
                  </div>
                )}

                {!isOnline && (
                  <div className="p-3 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg">
                    <p className="text-sm text-blue-700 dark:text-blue-300">
                      You're working offline. Changes will sync when connection is restored.
                    </p>
                  </div>
                )}

                {syncStatus.conflicts.length > 0 && (
                  <div className="p-3 bg-orange-50 dark:bg-orange-900/20 border border-orange-200 dark:border-orange-800 rounded-lg">
                    <p className="text-sm text-orange-700 dark:text-orange-300">
                      {syncStatus.conflicts.length} sync conflicts detected. Manual resolution required.
                    </p>
                  </div>
                )}
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </motion.div>
    </AnimatePresence>
  );
};

export default OfflineIndicator;
