import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { Loader, AlertTriangle, Zap, BrainCircuit, Crown, RefreshCw, Sparkles, Target, TrendingUp, CheckCircle, Clock, Calendar, AlertCircle } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { z } from 'zod';
import type { Task, CalendarEvent, AIInsight } from '../types';
import TypewriterText from './TypewriterText';
import RadialProgress from './RadialProgress';
import SkeletonLoader from './SkeletonLoader';
import ErrorToast from './ErrorToast';

interface PriorityCardProps {
  tasks: Task[];
  events: CalendarEvent[];
  onInspect: (insight: AIInsight) => void;
}

// Zod schema for AI response validation
const AIInsightSchema = z.object({
  action: z.string(),
  reasoning: z.array(z.string()),
  confidence: z.number().min(0).max(100)
});

// Debounce hook for AI calls
const useDebounce = (value: any, delay: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);

  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    return () => {
      clearTimeout(handler);
    };
  }, [value, delay]);

  return debouncedValue;
};

const PriorityCard: React.FC<PriorityCardProps> = React.memo(({ tasks, events, onInspect }) => {
  const { client, subscriptionStatus, subscribe, usage } = useSubscribeDev();
  const [insight, setInsight] = useState<AIInsight | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<{
    type: string;
    message: string;
    code?: string;
    retryAfter?: number;
  } | null>(null);
  const [showTyping, setShowTyping] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [lastRefresh, setLastRefresh] = useState<Date>(new Date());
  const [retryCount, setRetryCount] = useState(0);

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;

  const safeTasks = Array.isArray(tasks) ? tasks : [];
  const safeEvents = Array.isArray(events) ? events : [];

  // Debounce data changes to prevent excessive API calls
  const debouncedTasks = useDebounce(safeTasks, 1000);
  const debouncedEvents = useDebounce(safeEvents, 1000);

  // Memoize urgency calculations
  const urgencyMetrics = useMemo(() => {
    const now = new Date();
    
    const urgentTasks = safeTasks.filter(task => {
      const dueDate = new Date(task.dueDate);
      const hoursUntilDue = (dueDate.getTime() - now.getTime()) / (1000 * 60 * 60);
      return hoursUntilDue <= 24 && hoursUntilDue > 0;
    });

    const overdueTasks = safeTasks.filter(task => {
      const dueDate = new Date(task.dueDate);
      return dueDate < now;
    });

    const upcomingEvents = safeEvents.filter(event => {
      const eventTime = new Date();
      const [hours, minutes] = event.startTime.split(':');
      eventTime.setHours(parseInt(hours), parseInt(minutes), 0, 0);
      const hoursUntilEvent = (eventTime.getTime() - now.getTime()) / (1000 * 60 * 60);
      return hoursUntilEvent <= 2 && hoursUntilEvent > 0;
    });

    return { urgentTasks, overdueTasks, upcomingEvents };
  }, [safeTasks, safeEvents]);

  const generateInsight = useCallback(async () => {
    if (!client) return;

    setIsLoading(true);
    setError(null);
    setInsight(null);
    setShowTyping(false);
    setLastRefresh(new Date());

    const currentTime = new Date();
    const timeContext = {
      hour: currentTime.getHours(),
      dayOfWeek: currentTime.toLocaleDateString('en-US', { weekday: 'long' }),
      date: currentTime.toLocaleDateString()
    };

    const prompt = `
      As an autonomous personal agent, analyze the following user data to determine the single most important action they should take right now.

      Current Context:
      - Time: ${currentTime.toISOString()}
      - Day: ${timeContext.dayOfWeek}
      - Hour: ${timeContext.hour}:${currentTime.getMinutes().toString().padStart(2, '0')}

      Tasks Analysis:
      Total Tasks: ${debouncedTasks.length}
      Urgent Tasks (due within 24h): ${urgencyMetrics.urgentTasks.length}
      Overdue Tasks: ${urgencyMetrics.overdueTasks.length}
      
      Task Details:
      ${debouncedTasks.map(t => `- "${t.title}" (Due: ${t.dueDate}, Priority: ${t.priority})`).join('\n')}

      Calendar Events Today:
      Upcoming Events (within 2h): ${urgencyMetrics.upcomingEvents.length}
      
      Event Details:
      ${debouncedEvents.map(e => `- "${e.title}" (Time: ${e.startTime} - ${e.endTime})`).join('\n')}

      Context Factors:
      - Time of day suggests: ${timeContext.hour < 12 ? 'morning planning and high-focus work' : timeContext.hour < 17 ? 'afternoon execution and meetings' : 'evening review and preparation'}
      - Urgency level: ${urgencyMetrics.overdueTasks.length > 0 ? 'HIGH (overdue items)' : urgencyMetrics.urgentTasks.length > 0 ? 'MEDIUM (urgent items)' : 'LOW (normal planning)'}

      Provide a JSON response with:
      {
        "action": "A specific, actionable recommendation (max 80 characters)",
        "reasoning": [
          "3-5 bullet points explaining your logic",
          "Reference specific tasks, times, or priorities",
          "Include time-sensitive considerations",
          "Mention any conflicts or dependencies"
        ],
        "confidence": "Number 0-100 based on data quality and urgency clarity"
      }
    `;

    try {
      const response = await client.run('openai/gpt-4o', {
        input: {
          messages: [{ role: 'user', content: prompt }],
        },
        response_format: { type: 'json_object' },
      });

      const rawResult = response.output[0] as any;
      
      // Validate the response using Zod
      const validationResult = AIInsightSchema.safeParse(rawResult);
      
      if (!validationResult.success) {
        throw new Error('Invalid AI response format');
      }
      
      const result = validationResult.data;
      
      const safeInsight: AIInsight = {
        action: result.action || 'Focus on your highest priority task right now! 🎯',
        reasoning: Array.isArray(result.reasoning) ? result.reasoning : ['AI analysis complete with available data'],
        confidence: typeof result.confidence === 'number' ? Math.min(100, Math.max(0, result.confidence)) : 85
      };
      
      setInsight(safeInsight);
      setShowTyping(true);
      setRetryCount(0);
      
      if (safeInsight.confidence >= 90) {
        setTimeout(() => setShowCelebration(true), 2000);
        setTimeout(() => setShowCelebration(false), 4000);
      }
    } catch (err: any) {
      const errorDetails = {
        type: err.type || 'unknown_error',
        message: err.message || 'Failed to generate insight. Please try again.',
        code: err.code,
        retryAfter: err.retryAfter,
      };

      setError(errorDetails);
      console.error('AI Insight Error:', err);
    } finally {
      setIsLoading(false);
    }
  }, [client, debouncedTasks, debouncedEvents, urgencyMetrics]);

  // Auto-retry with exponential backoff
  const handleRetry = useCallback(() => {
    const delay = Math.min(1000 * Math.pow(2, retryCount), 10000); // Max 10 seconds
    setRetryCount(prev => prev + 1);
    
    setTimeout(() => {
      generateInsight();
    }, delay);
  }, [generateInsight, retryCount]);

  useEffect(() => {
    if (isPremiumUser) {
      generateInsight();
    } else {
      setIsLoading(false);
    }
  }, [isPremiumUser, generateInsight]);

  const handleInspect = useCallback(() => {
    if (insight) {
      onInspect(insight);
    }
  }, [insight, onInspect]);

  const cardVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 }
  };

  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 90) return 'text-emerald-500';
    if (confidence >= 75) return 'text-blue-500';
    if (confidence >= 60) return 'text-amber-500';
    return 'text-red-500';
  };

  const getUrgencyIndicator = () => {
    if (urgencyMetrics.overdueTasks.length > 0) {
      return {
        icon: AlertCircle,
        color: 'text-red-500',
        bg: 'bg-red-50 dark:bg-red-900/20',
        border: 'border-red-200 dark:border-red-800',
        text: `${urgencyMetrics.overdueTasks.length} overdue`,
        pulse: true
      };
    }
    if (urgencyMetrics.urgentTasks.length > 0) {
      return {
        icon: Clock,
        color: 'text-amber-500',
        bg: 'bg-amber-50 dark:bg-amber-900/20',
        border: 'border-amber-200 dark:border-amber-800',
        text: `${urgencyMetrics.urgentTasks.length} urgent`,
        pulse: false
      };
    }
    if (urgencyMetrics.upcomingEvents.length > 0) {
      return {
        icon: Calendar,
        color: 'text-blue-500',
        bg: 'bg-blue-50 dark:bg-blue-900/20',
        border: 'border-blue-200 dark:border-blue-800',
        text: `${urgencyMetrics.upcomingEvents.length} upcoming`,
        pulse: false
      };
    }
    return null;
  };

  const urgencyInfo = getUrgencyIndicator();

  if (!isPremiumUser) {
    return (
      <motion.div 
        className="glass-card p-8 relative overflow-hidden group"
        variants={cardVariants}
        initial="hidden"
        animate="visible"
        transition={{ duration: 0.6, ease: "easeOut" }}
      >
        <div className="absolute inset-0 bg-gradient-to-br from-purple-600/5 to-pink-600/5 dark:from-purple-600/10 dark:to-pink-600/10 backdrop-blur-sm z-0"></div>
        <div className="absolute inset-0 bg-grid-pattern opacity-20 dark:opacity-10"></div>
        
        <div className="relative z-10 text-center flex flex-col items-center">
          <motion.div 
            className="inline-flex items-center justify-center w-20 h-20 glass rounded-2xl mb-6 group-hover:scale-110 transition-transform duration-300"
            whileHover={{ rotate: 10 }}
          >
            <Crown className="w-10 h-10 text-gradient" />
          </motion.div>
          <h3 className="text-2xl font-black text-gradient mb-3">Unlock Proactive Intelligence</h3>
          <p className="text-gray-600 dark:text-gray-400 mb-8 max-w-md mx-auto leading-relaxed">
            Let Aura analyze your tasks, calendar, and priorities to find the single most important action you should take right now.
          </p>
          
          <div className="w-full max-w-sm p-6 glass-card border border-dashed border-gray-300 dark:border-gray-700 mb-8">
            <div className="flex items-center gap-3 mb-3">
              <Target className="w-5 h-5 text-purple-500" />
              <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">AI Recommendation</span>
            </div>
            <p className="font-bold text-lg text-gray-900 dark:text-white blur-sm select-none">Complete the Phoenix Project proposal before 3 PM meeting</p>
            <div className="flex items-center gap-2 mt-3">
              <div className="w-8 h-8 rounded-full bg-gradient-to-r from-emerald-500 to-green-600 flex items-center justify-center">
                <span className="text-white text-xs font-bold blur-sm">94</span>
              </div>
              <span className="text-sm text-gray-500 dark:text-gray-400 blur-sm">High confidence</span>
            </div>
          </div>

          <motion.button
            onClick={subscribe}
            className="btn-primary group"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <span className="flex items-center gap-2">
              Unlock Premium Intelligence
              <Sparkles className="w-5 h-5 group-hover:rotate-12 transition-transform duration-200" />
            </span>
          </motion.button>
          
          <p className="text-xs text-gray-500 dark:text-gray-400 mt-4">
            Join thousands using AI to optimize their daily priorities
          </p>
        </div>
      </motion.div>
    );
  }

  if (isLoading) {
    return (
      <motion.div 
        className="glass-card p-8 min-h-[28rem]"
        variants={cardVariants}
        initial="hidden"
        animate="visible"
      >
        <div className="space-y-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <SkeletonLoader variant="avatar" />
              <div className="space-y-2">
                <SkeletonLoader width={120} height={16} />
                <SkeletonLoader width={80} height={12} />
              </div>
            </div>
            <SkeletonLoader width={60} height={32} />
          </div>
          
          <SkeletonLoader width="100%" height={20} />
          <SkeletonLoader width="80%" height={20} />
          
          <div className="flex items-center gap-6">
            <SkeletonLoader variant="avatar" width={80} height={80} />
            <div className="flex-1 space-y-3">
              <SkeletonLoader width="100%" height={12} />
              <SkeletonLoader width="90%" height={12} />
              <SkeletonLoader width="70%" height={12} />
            </div>
          </div>
          
          <div className="flex justify-between">
            <SkeletonLoader width={100} height={12} />
            <SkeletonLoader width={120} height={12} />
          </div>
        </div>
      </motion.div>
    );
  }

  if (!insight) return null;

  return (
    <>
      <motion.div 
        className="glass-card p-8 relative overflow-hidden group min-h-[28rem]"
        variants={cardVariants}
        initial="hidden"
        animate="visible"
      >
        <div className="absolute inset-0 bg-gradient-to-br from-purple-600/5 to-indigo-600/5"></div>
        
        <AnimatePresence>
          {showCelebration && (
            <motion.div
              className="absolute inset-0 flex items-center justify-center pointer-events-none z-20"
              initial={{ scale: 0, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0, opacity: 0 }}
              transition={{ type: "spring", stiffness: 500, damping: 15 }}
            >
              <div className="text-6xl">🎉</div>
            </motion.div>
          )}
        </AnimatePresence>

        <div className="relative z-10 flex flex-col h-full">
          <div className="flex justify-between items-start mb-6">
            <div className="flex items-center gap-3">
              <motion.div 
                className="w-12 h-12 gradient-primary rounded-2xl flex items-center justify-center shadow-lg" 
                whileHover={{ scale: 1.1, rotate: 5 }}
              >
                <Target className="w-6 h-6 text-white" />
              </motion.div>
              <div>
                <h3 className="text-sm font-black text-gradient uppercase tracking-wider">
                  Priority Intelligence
                </h3>
                <p className="text-xs text-gray-500 dark:text-gray-400">
                  Last updated: {lastRefresh.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                </p>
              </div>
            </div>
            
            <div className="flex gap-2">
              <motion.button 
                onClick={generateInsight} 
                className="btn-secondary p-3" 
                whileHover={{ scale: 1.1, rotate: 180 }} 
                whileTap={{ scale: 0.9 }}
                title="Refresh analysis"
              >
                <RefreshCw className="w-4 h-4" />
              </motion.button>
              <motion.button 
                onClick={handleInspect} 
                className="btn-secondary px-4 py-3 flex items-center gap-2" 
                whileHover={{ scale: 1.05 }} 
                whileTap={{ scale: 0.95 }}
              >
                <BrainCircuit className="w-4 h-4" />
                <span className="text-sm font-medium">Analyze</span>
              </motion.button>
            </div>
          </div>

          {urgencyInfo && (
            <motion.div 
              className={`flex items-center gap-3 p-3 rounded-xl mb-6 ${urgencyInfo.bg} ${urgencyInfo.border} border`}
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.2 }}
            >
              <urgencyInfo.icon className={`w-5 h-5 ${urgencyInfo.color} ${urgencyInfo.pulse ? 'animate-pulse' : ''}`} />
              <span className={`text-sm font-semibold ${urgencyInfo.color}`}>
                {urgencyInfo.text.toUpperCase()}
              </span>
            </motion.div>
          )}
          
          <div className="flex-grow">
            <div className="text-2xl md:text-3xl font-bold text-gray-900 dark:text-white leading-tight mb-6">
              {showTyping ? <TypewriterText text={insight.action} speed={30} /> : insight.action}
            </div>
            
            <motion.div 
              className="glass p-6 rounded-xl border border-purple-200/50 dark:border-purple-800/50"
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.5 }}
            >
              <div className="flex items-start gap-6">
                <div className="flex-shrink-0">
                  <RadialProgress 
                    percentage={insight.confidence} 
                    colorClass={getConfidenceColor(insight.confidence)}
                    Icon={Zap} 
                    label="Confidence"
                    size={80}
                    strokeWidth={6}
                  />
                </div>
                
                <div className="flex-1 min-w-0">
                  <h4 className="font-semibold text-gray-800 dark:text-gray-200 mb-3 flex items-center gap-2">
                    <CheckCircle className="w-4 h-4 text-green-500" />
                    AI Reasoning
                  </h4>
                  <ul className="space-y-2 text-sm text-gray-600 dark:text-gray-400">
                    {insight.reasoning.slice(0, 3).map((reason, index) => (
                      <motion.li 
                        key={index} 
                        className="flex items-start gap-2"
                        initial={{ opacity: 0, x: -10 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ delay: 0.7 + index * 0.1 }}
                      >
                        <div className="w-1.5 h-1.5 bg-purple-500 rounded-full mt-2 flex-shrink-0"></div>
                        <span>{reason}</span>
                      </motion.li>
                    ))}
                  </ul>
                </div>
              </div>
            </motion.div>
          </div>

          {usage && (
            <motion.div 
              className="mt-6 pt-4 border-t border-gray-200 dark:border-gray-700"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1.5 }}
            >
              <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400">
                <div className="flex items-center gap-2">
                  <TrendingUp className="w-3 h-3" />
                  <span>Insights generated: {usage.usedCredits.toLocaleString()}</span>
                </div>
                <div className="flex items-center gap-2">
                  <Zap className="w-3 h-3" />
                  <span>Credits remaining: {usage.remainingCredits.toLocaleString()}</span>
                </div>
              </div>
            </motion.div>
          )}
        </div>
      </motion.div>

      {error && (
        <ErrorToast
          error={error}
          onClose={() => setError(null)}
          onRetry={handleRetry}
        />
      )}
    </>
  );
});

PriorityCard.displayName = 'PriorityCard';

export default PriorityCard;
