import { useState, useEffect, useCallback } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import type { PredictiveInsight, CrossDomainCorrelation, HealthMetric, Transaction, Task } from '../types';

export const usePredictiveAnalytics = () => {
  const { client, useStorage, subscriptionStatus } = useSubscribeDev();
  const [insights, setInsights] = useStorage<PredictiveInsight[]>('predictive_insights', []);
  const [correlations, setCorrelations] = useStorage<CrossDomainCorrelation[]>('cross_domain_correlations', []);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [lastAnalysis, setLastAnalysis] = useStorage<Date>('last_predictive_analysis', new Date());

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;

  // Get historical data for analysis
  const [healthMetrics] = useStorage<HealthMetric[]>('health_metrics', []);
  const [transactions] = useStorage<Transaction[]>('recent_transactions', []);
  const [tasks] = useStorage<Task[]>('user_tasks', []);

  const generatePredictiveInsights = useCallback(async () => {
    if (!client || !isPremiumUser || isAnalyzing) return;

    setIsAnalyzing(true);

    try {
      // Analyze productivity patterns
      const productivityInsight = await client.run('openai/gpt-4o', {
        input: {
          messages: [{
            role: 'user',
            content: `Analyze this historical data to predict productivity patterns:
            
            Recent Tasks: ${tasks.slice(0, 10).map(t => `${t.title} (${t.priority}, due: ${t.dueDate})`).join(', ')}
            Health Metrics: ${healthMetrics.slice(0, 5).map(m => `${m.type}: ${m.value}${m.unit}`).join(', ')}
            
            Predict productivity trends for the next week and provide actionable insights.
            
            Respond in JSON format:
            {
              "prediction": "specific prediction about productivity",
              "confidence": number 0-100,
              "basedOn": ["data points used"],
              "timeframe": "next week",
              "actionable": true/false
            }`
          }]
        },
        response_format: { type: 'json_object' }
      });

      const productivityData = productivityInsight.output[0] as any;
      
      // Analyze financial patterns
      const financialInsight = await client.run('openai/gpt-4o', {
        input: {
          messages: [{
            role: 'user',
            content: `Analyze spending patterns to predict financial trends:
            
            Recent Transactions: ${transactions.slice(0, 10).map(t => `${t.description}: $${t.amount} (${t.category})`).join(', ')}
            
            Predict spending patterns and budget recommendations for next month.
            
            Respond in JSON format:
            {
              "prediction": "specific financial prediction",
              "confidence": number 0-100,
              "basedOn": ["transaction patterns", "spending categories"],
              "timeframe": "next month",
              "actionable": true
            }`
          }]
        },
        response_format: { type: 'json_object' }
      });

      const financialData = financialInsight.output[0] as any;

      // Create insights
      const newInsights: PredictiveInsight[] = [
        {
          id: `productivity-${Date.now()}`,
          type: 'productivity',
          prediction: productivityData.prediction || 'Productivity analysis complete',
          confidence: productivityData.confidence || 75,
          basedOn: productivityData.basedOn || ['task completion patterns', 'health metrics'],
          timeframe: productivityData.timeframe || 'next week',
          actionable: productivityData.actionable || true,
          createdAt: new Date(),
        },
        {
          id: `financial-${Date.now()}`,
          type: 'financial',
          prediction: financialData.prediction || 'Financial analysis complete',
          confidence: financialData.confidence || 80,
          basedOn: financialData.basedOn || ['spending patterns', 'transaction history'],
          timeframe: financialData.timeframe || 'next month',
          actionable: financialData.actionable || true,
          createdAt: new Date(),
        },
      ];

      const updatedInsights = [...newInsights, ...insights.slice(0, 8)]; // Keep last 10 insights
      setInsights(updatedInsights);
      setLastAnalysis(new Date());

    } catch (error) {
      console.error('Failed to generate predictive insights:', error);
    } finally {
      setIsAnalyzing(false);
    }
  }, [client, isPremiumUser, isAnalyzing, tasks, healthMetrics, transactions, insights, setInsights, setLastAnalysis]);

  const generateCrossDomainCorrelations = useCallback(async () => {
    if (!client || !isPremiumUser || isAnalyzing) return;

    try {
      const correlationAnalysis = await client.run('openai/gpt-4o', {
        input: {
          messages: [{
            role: 'user',
            content: `Analyze correlations between different life domains:
            
            Sleep Data: ${healthMetrics.filter(m => m.type === 'sleep').slice(0, 7).map(m => `${m.value}h`).join(', ')}
            Step Data: ${healthMetrics.filter(m => m.type === 'steps').slice(0, 7).map(m => m.value).join(', ')}
            Task Completion: ${tasks.filter(t => new Date(t.dueDate) < new Date()).length} completed recently
            Spending: ${transactions.slice(0, 10).map(t => t.amount).join(', ')}
            
            Find correlations between sleep, activity, productivity, and spending.
            
            Respond in JSON format:
            {
              "correlations": [
                {
                  "domains": ["sleep", "productivity"],
                  "correlation": number -1 to 1,
                  "insight": "explanation of correlation",
                  "strength": "weak/moderate/strong",
                  "dataPoints": number
                }
              ]
            }`
          }]
        },
        response_format: { type: 'json_object' }
      });

      const correlationData = correlationAnalysis.output[0] as any;
      
      if (correlationData.correlations) {
        const newCorrelations: CrossDomainCorrelation[] = correlationData.correlations.map((corr: any, index: number) => ({
          id: `correlation-${Date.now()}-${index}`,
          domains: corr.domains || ['sleep', 'productivity'],
          correlation: corr.correlation || 0.5,
          insight: corr.insight || 'Correlation detected between domains',
          strength: corr.strength || 'moderate',
          timeframe: 'last 30 days',
          dataPoints: corr.dataPoints || 10,
        }));

        setCorrelations(newCorrelations);
      }

    } catch (error) {
      console.error('Failed to generate correlations:', error);
    }
  }, [client, isPremiumUser, isAnalyzing, healthMetrics, tasks, transactions, setCorrelations]);

  // Auto-generate insights periodically
  useEffect(() => {
    const shouldAnalyze = isPremiumUser && 
      Date.now() - new Date(lastAnalysis).getTime() > 24 * 60 * 60 * 1000; // 24 hours

    if (shouldAnalyze) {
      generatePredictiveInsights();
      generateCrossDomainCorrelations();
    }
  }, [isPremiumUser, lastAnalysis, generatePredictiveInsights, generateCrossDomainCorrelations]);

  return {
    insights,
    correlations,
    isAnalyzing,
    lastAnalysis,
    generatePredictiveInsights,
    generateCrossDomainCorrelations,
  };
};
