import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Sidebar from './Sidebar';
import CommandCenter from './CommandCenter';
import TransparencyPanel from './TransparencyPanel';
import FloatingChat from './FloatingChat';
import type { AIInsight, TransparencyInfo } from '../types';

const MainLayout: React.FC = () => {
  const [transparencyInfo, setTransparencyInfo] = useState<TransparencyInfo | null>(null);
  const [isSidebarOpen, setSidebarOpen] = useState(false);

  const handleInspectInsight = (insight: AIInsight) => {
    setTransparencyInfo({
      ...insight,
      decision: insight.action,
    });
  };

  const handleClosePanel = () => {
    setTransparencyInfo(null);
  };

  const pageVariants = {
    initial: { opacity: 0, y: 20 },
    in: { opacity: 1, y: 0 },
    out: { opacity: 0, y: -20 }
  };

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-950 text-gray-900 dark:text-white overflow-hidden">
      <Sidebar isOpen={isSidebarOpen} setIsOpen={setSidebarOpen} />
      
      <motion.main 
        className="flex-1 flex flex-col relative min-w-0 md:ml-72 transition-transform duration-300"
        variants={pageVariants}
        initial="initial"
        animate="in"
        exit="out"
        transition={{ duration: 0.5, ease: "easeInOut" }}
      >
        <CommandCenter onInspectInsight={handleInspectInsight} onMenuClick={() => setSidebarOpen(true)} />
      </motion.main>
      
      <AnimatePresence>
        {transparencyInfo && (
          <TransparencyPanel info={transparencyInfo} onClose={handleClosePanel} />
        )}
      </AnimatePresence>
      
      <FloatingChat />
    </div>
  );
};

export default MainLayout;
