import React from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { Check, Crown, Zap, Users, Star } from 'lucide-react';
import { motion } from 'framer-motion';
import type { SubscriptionTier } from '../types';

const SubscriptionTiers: React.FC = React.memo(() => {
  const { subscribe, subscriptionStatus } = useSubscribeDev();

  const tiers: SubscriptionTier[] = [
    {
      id: 'free',
      name: 'Free',
      price: 0,
      credits: 100,
      features: [
        'Basic dashboard',
        'Task management',
        'Limited AI insights (5/day)',
        'Basic transparency reports',
        'Email support'
      ],
      integrations: ['None']
    },
    {
      id: 'pro',
      name: 'Pro',
      price: 29,
      credits: 1000,
      popular: true,
      features: [
        'Everything in Free',
        'Financial integration (Plaid)',
        'Health app integration',
        'Email monitoring',
        'Unlimited AI insights',
        'Advanced analytics',
        'Priority support',
        'Custom dashboard layouts'
      ],
      integrations: ['Bank accounts', 'Health apps', '1 social media account']
    },
    {
      id: 'enterprise',
      name: 'Enterprise',
      price: 99,
      credits: -1, // Unlimited
      features: [
        'Everything in Pro',
        'Multiple bank accounts',
        'Multiple health integrations',
        'Full social media monitoring',
        'Team collaboration',
        'Custom integrations',
        'API access',
        'Dedicated support',
        'Advanced security features',
        'Custom AI models'
      ],
      integrations: ['Unlimited integrations', 'Custom APIs', 'Team features']
    }
  ];

  const currentTier = subscriptionStatus?.hasActiveSubscription ? 'pro' : 'free';

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const cardVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 }
  };

  return (
    <div className="space-y-8">
      <motion.div 
        className="text-center"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <h2 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">Choose Your Plan</h2>
        <p className="text-xl text-gray-600 dark:text-gray-400 max-w-2xl mx-auto">
          Unlock the full power of your AI assistant with transparent pricing and no hidden fees
        </p>
      </motion.div>

      <motion.div 
        className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-6xl mx-auto"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        {tiers.map((tier, index) => (
          <motion.div
            key={tier.id}
            className={`relative bg-white dark:bg-gray-800/50 border rounded-3xl p-8 transition-all duration-300 hover:shadow-2xl hover:scale-[1.02] ${
              tier.popular
                ? 'border-purple-500 dark:border-purple-400 ring-2 ring-purple-500/20 shadow-xl'
                : 'border-gray-200 dark:border-gray-700'
            } ${currentTier === tier.id ? 'ring-2 ring-green-500/20 border-green-500' : ''}`}
            variants={cardVariants}
            whileHover={{ y: -5 }}
          >
            {tier.popular && (
              <motion.div 
                className="absolute -top-4 left-1/2 transform -translate-x-1/2"
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.5, type: 'spring', stiffness: 500, damping: 15 }}
              >
                <div className="bg-gradient-to-r from-purple-600 to-purple-700 text-white px-6 py-2 rounded-full text-sm font-bold flex items-center gap-2 shadow-lg">
                  <Star className="w-4 h-4" />
                  Most Popular
                </div>
              </motion.div>
            )}

            {currentTier === tier.id && (
              <motion.div 
                className="absolute -top-4 right-4"
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.3, type: 'spring', stiffness: 500, damping: 15 }}
              >
                <div className="bg-green-500 text-white px-4 py-2 rounded-full text-sm font-bold shadow-lg">
                  Current Plan
                </div>
              </motion.div>
            )}

            <div className="text-center mb-8">
              <div className="flex items-center justify-center mb-6">
                <motion.div
                  className={`w-16 h-16 rounded-2xl flex items-center justify-center ${
                    tier.id === 'free' ? 'bg-gradient-to-br from-gray-500 to-gray-600' :
                    tier.id === 'pro' ? 'bg-gradient-to-br from-purple-500 to-purple-600' :
                    'bg-gradient-to-br from-blue-500 to-blue-600'
                  } shadow-lg`}
                  whileHover={{ scale: 1.1, rotate: 5 }}
                  transition={{ type: 'spring', stiffness: 400, damping: 10 }}
                >
                  {tier.id === 'free' && <Zap className="w-8 h-8 text-white" />}
                  {tier.id === 'pro' && <Crown className="w-8 h-8 text-white" />}
                  {tier.id === 'enterprise' && <Users className="w-8 h-8 text-white" />}
                </motion.div>
              </div>
              
              <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">{tier.name}</h3>
              
              <div className="flex items-baseline justify-center gap-2 mb-2">
                <span className="text-5xl font-bold text-gray-900 dark:text-white">${tier.price}</span>
                <span className="text-gray-500 dark:text-gray-400 text-lg">/month</span>
              </div>
              
              <p className="text-sm text-gray-500 dark:text-gray-400 font-medium">
                {tier.credits === -1 ? 'Unlimited' : tier.credits.toLocaleString()} AI credits
              </p>
              
              {tier.price > 0 && (
                <p className="text-xs text-gray-400 mt-2">
                  Billed monthly • Cancel anytime
                </p>
              )}
            </div>

            <div className="space-y-6 mb-8">
              <div>
                <h4 className="font-bold text-gray-900 dark:text-white mb-4 text-lg">Features</h4>
                <ul className="space-y-3">
                  {tier.features.map((feature, featureIndex) => (
                    <motion.li 
                      key={featureIndex} 
                      className="flex items-start gap-3"
                      initial={{ opacity: 0, x: -10 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.1 * featureIndex }}
                    >
                      <Check className="w-5 h-5 text-green-500 flex-shrink-0 mt-0.5" />
                      <span className="text-gray-600 dark:text-gray-400 leading-relaxed">{feature}</span>
                    </motion.li>
                  ))}
                </ul>
              </div>

              <div>
                <h4 className="font-bold text-gray-900 dark:text-white mb-4 text-lg">Integrations</h4>
                <ul className="space-y-3">
                  {tier.integrations.map((integration, integrationIndex) => (
                    <motion.li 
                      key={integrationIndex} 
                      className="flex items-start gap-3"
                      initial={{ opacity: 0, x: -10 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.1 * integrationIndex }}
                    >
                      <Check className="w-5 h-5 text-blue-500 flex-shrink-0 mt-0.5" />
                      <span className="text-gray-600 dark:text-gray-400 leading-relaxed">{integration}</span>
                    </motion.li>
                  ))}
                </ul>
              </div>
            </div>

            <motion.button
              onClick={subscribe}
              disabled={currentTier === tier.id}
              className={`w-full py-4 px-6 rounded-2xl font-bold text-lg transition-all duration-200 ${
                currentTier === tier.id
                  ? 'bg-gray-200 dark:bg-gray-700 text-gray-500 dark:text-gray-400 cursor-not-allowed'
                  : tier.popular
                  ? 'bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-500 hover:to-purple-600 text-white shadow-lg hover:shadow-xl'
                  : 'bg-gray-900 dark:bg-white hover:bg-gray-800 dark:hover:bg-gray-100 text-white dark:text-gray-900 shadow-lg hover:shadow-xl'
              }`}
              whileHover={currentTier !== tier.id ? { scale: 1.02 } : {}}
              whileTap={currentTier !== tier.id ? { scale: 0.98 } : {}}
            >
              {currentTier === tier.id ? 'Current Plan' : tier.id === 'free' ? 'Get Started Free' : 'Upgrade Now'}
            </motion.button>
          </motion.div>
        ))}
      </motion.div>

      <motion.div 
        className="text-center space-y-4"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.8 }}
      >
        <div className="flex items-center justify-center gap-8 text-sm text-gray-500 dark:text-gray-400">
          <div className="flex items-center gap-2">
            <Check className="w-4 h-4 text-green-500" />
            <span>Bank-level security</span>
          </div>
          <div className="flex items-center gap-2">
            <Check className="w-4 h-4 text-green-500" />
            <span>24/7 uptime monitoring</span>
          </div>
          <div className="flex items-center gap-2">
            <Check className="w-4 h-4 text-green-500" />
            <span>Cancel anytime</span>
          </div>
        </div>
        
        <p className="text-gray-500 dark:text-gray-400 max-w-2xl mx-auto">
          All plans include secure data encryption, transparent AI decision-making, and our commitment to your privacy. 
          No hidden fees, no vendor lock-in.
        </p>
        
        <div className="pt-4">
          <p className="text-sm text-gray-400">
            Questions? <a href="mailto:support@aura.ai" className="text-purple-600 dark:text-purple-400 hover:underline">Contact our team</a>
          </p>
        </div>
      </motion.div>
    </div>
  );
});

SubscriptionTiers.displayName = 'SubscriptionTiers';

export default SubscriptionTiers;
