import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, AlertCircle, RefreshCw, ExternalLink, Copy } from 'lucide-react';

interface ErrorToastProps {
  error: {
    type: string;
    message: string;
    code?: string;
    retryAfter?: number;
    actionUrl?: string;
  };
  onClose: () => void;
  onRetry?: () => void;
  autoClose?: boolean;
  duration?: number;
}

const ErrorToast: React.FC<ErrorToastProps> = ({
  error,
  onClose,
  onRetry,
  autoClose = true,
  duration = 5000,
}) => {
  const [timeLeft, setTimeLeft] = useState(duration);

  useEffect(() => {
    if (!autoClose) return;

    const timer = setInterval(() => {
      setTimeLeft((prev) => {
        if (prev <= 100) {
          onClose();
          return 0;
        }
        return prev - 100;
      });
    }, 100);

    return () => clearInterval(timer);
  }, [autoClose, onClose]);

  const getErrorDetails = () => {
    switch (error.type) {
      case 'insufficient_credits':
        return {
          title: 'Insufficient Credits',
          description: 'You need more credits to continue using AI features.',
          actionText: 'Upgrade Plan',
          actionIcon: ExternalLink,
          severity: 'warning',
        };
      case 'rate_limit_exceeded':
        return {
          title: 'Rate Limit Exceeded',
          description: `Please wait ${Math.ceil((error.retryAfter || 0) / 1000)} seconds before trying again.`,
          actionText: 'Retry',
          actionIcon: RefreshCw,
          severity: 'info',
        };
      case 'network_error':
        return {
          title: 'Connection Error',
          description: 'Unable to connect to our servers. Please check your internet connection.',
          actionText: 'Retry',
          actionIcon: RefreshCw,
          severity: 'error',
        };
      case 'validation_error':
        return {
          title: 'Invalid Data',
          description: 'The information provided is not valid. Please check and try again.',
          actionText: 'Dismiss',
          actionIcon: X,
          severity: 'warning',
        };
      default:
        return {
          title: 'Something went wrong',
          description: error.message || 'An unexpected error occurred.',
          actionText: 'Retry',
          actionIcon: RefreshCw,
          severity: 'error',
        };
    }
  };

  const details = getErrorDetails();
  const ActionIcon = details.actionIcon;

  const getSeverityStyles = () => {
    switch (details.severity) {
      case 'warning':
        return {
          bg: 'bg-amber-50 dark:bg-amber-900/20',
          border: 'border-amber-200 dark:border-amber-800',
          icon: 'text-amber-500',
          progress: 'bg-amber-500',
        };
      case 'info':
        return {
          bg: 'bg-blue-50 dark:bg-blue-900/20',
          border: 'border-blue-200 dark:border-blue-800',
          icon: 'text-blue-500',
          progress: 'bg-blue-500',
        };
      default:
        return {
          bg: 'bg-red-50 dark:bg-red-900/20',
          border: 'border-red-200 dark:border-red-800',
          icon: 'text-red-500',
          progress: 'bg-red-500',
        };
    }
  };

  const styles = getSeverityStyles();

  const handleAction = () => {
    if (details.actionText === 'Retry' && onRetry) {
      onRetry();
    } else if (details.actionText === 'Upgrade Plan' && error.actionUrl) {
      window.open(error.actionUrl, '_blank');
    }
    onClose();
  };

  const copyErrorCode = () => {
    if (error.code) {
      navigator.clipboard.writeText(error.code);
    }
  };

  return (
    <motion.div
      className={`fixed top-4 right-4 max-w-md w-full glass-card ${styles.bg} ${styles.border} border z-50`}
      initial={{ opacity: 0, x: 300, scale: 0.9 }}
      animate={{ opacity: 1, x: 0, scale: 1 }}
      exit={{ opacity: 0, x: 300, scale: 0.9 }}
      transition={{ type: 'spring', stiffness: 500, damping: 30 }}
    >
      <div className="p-4">
        <div className="flex items-start gap-3">
          <AlertCircle className={`w-5 h-5 ${styles.icon} flex-shrink-0 mt-0.5`} />
          <div className="flex-1 min-w-0">
            <h3 className="font-semibold text-gray-900 dark:text-white text-sm">
              {details.title}
            </h3>
            <p className="text-sm text-gray-600 dark:text-gray-400 mt-1 leading-relaxed">
              {details.description}
            </p>
            {error.code && (
              <div className="mt-2 flex items-center gap-2">
                <code className="text-xs bg-gray-100 dark:bg-gray-800 px-2 py-1 rounded font-mono">
                  {error.code}
                </code>
                <button
                  onClick={copyErrorCode}
                  className="text-xs text-gray-500 hover:text-gray-700 dark:hover:text-gray-300 transition-colors"
                  title="Copy error code"
                >
                  <Copy className="w-3 h-3" />
                </button>
              </div>
            )}
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300 transition-colors p-1"
          >
            <X className="w-4 h-4" />
          </button>
        </div>

        <div className="flex items-center justify-between mt-4">
          <motion.button
            onClick={handleAction}
            className="btn-secondary text-sm py-2 px-3 flex items-center gap-2"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <ActionIcon className="w-3 h-3" />
            {details.actionText}
          </motion.button>

          {autoClose && (
            <div className="text-xs text-gray-500 dark:text-gray-400">
              Auto-close in {Math.ceil(timeLeft / 1000)}s
            </div>
          )}
        </div>

        {autoClose && (
          <div className="mt-3 w-full bg-gray-200 dark:bg-gray-700 rounded-full h-1 overflow-hidden">
            <motion.div
              className={`h-1 ${styles.progress} rounded-full`}
              initial={{ width: '100%' }}
              animate={{ width: '0%' }}
              transition={{ duration: duration / 1000, ease: 'linear' }}
            />
          </div>
        )}
      </div>
    </motion.div>
  );
};

export default React.memo(ErrorToast);
