import React, { useState, useEffect } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { Mail, MessageSquare, AtSign, AlertCircle, TrendingUp, Loader } from 'lucide-react';
import type { CommunicationSummary } from '../types';
import TypewriterText from './TypewriterText';
import ErrorFallback from './ErrorFallback';

const CommunicationHub: React.FC = () => {
  const { client, useStorage, subscriptionStatus } = useSubscribeDev();
  
  const [commSummary] = useStorage<CommunicationSummary>('communication_summary', {
    emails: { total: 124, unread: 18, urgent: 5 },
    messages: { total: 47, unread: 12 },
    socialMedia: { mentions: 23, engagement: 156 }
  });

  const [aiInsight, setAiInsight] = useState<string>('');
  const [isLoadingInsight, setIsLoadingInsight] = useState(false);
  const [insightError, setInsightError] = useState<Error | null>(null);

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;

  const generateCommunicationInsight = async () => {
    if (!client || !isPremiumUser) return;

    setIsLoadingInsight(true);
    setInsightError(null);
    try {
      const response = await client.run('openai/gpt-4o', {
        input: {
          messages: [{
            role: 'user',
            content: `As a productivity AI, analyze this communication data and provide a brief, actionable insight:
            
            Emails: ${commSummary.emails.total} total, ${commSummary.emails.unread} unread, ${commSummary.emails.urgent} urgent
            Messages: ${commSummary.messages.total} total, ${commSummary.messages.unread} unread
            Social Media: ${commSummary.socialMedia.mentions} mentions, ${commSummary.socialMedia.engagement} engagement
            
            Provide one specific recommendation in 1-2 sentences to improve their communication efficiency.`
          }]
        }
      });

      setAiInsight(response.output[0] as string);
    } catch (error) {
      setInsightError(error as Error);
    } finally {
      setIsLoadingInsight(false);
    }
  };

  useEffect(() => {
    if (isPremiumUser) {
      generateCommunicationInsight();
    }
  }, [isPremiumUser]);

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-white">Communication Hub</h2>
        <div className="flex items-center gap-2 text-sm text-gray-500 dark:text-gray-400">
          <MessageSquare className="w-4 h-4" />
          <span>Last updated: {new Date().toLocaleDateString()}</span>
        </div>
      </div>

      {/* Communication Overview */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {/* Email Summary */}
        <div className="bg-white dark:bg-gray-800/50 border border-gray-200 dark:border-gray-700 rounded-xl p-6 transition-all duration-300 hover:shadow-lg">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-blue-100 dark:bg-blue-900/30 rounded-lg flex items-center justify-center">
                <Mail className="w-5 h-5 text-blue-600 dark:text-blue-400" />
              </div>
              <h3 className="font-semibold text-gray-900 dark:text-white">Email</h3>
            </div>
            {commSummary.emails.urgent > 0 && (
              <div className="flex items-center gap-1 text-red-500">
                <AlertCircle className="w-4 h-4" />
                <span className="text-sm font-medium">{commSummary.emails.urgent}</span>
              </div>
            )}
          </div>
          <div className="space-y-2">
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Total</span>
              <span className="font-semibold text-gray-900 dark:text-white">{commSummary.emails.total}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Unread</span>
              <span className="font-semibold text-orange-600 dark:text-orange-400">{commSummary.emails.unread}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Urgent</span>
              <span className="font-semibold text-red-600 dark:text-red-400">{commSummary.emails.urgent}</span>
            </div>
          </div>
        </div>

        {/* Messages Summary */}
        <div className="bg-white dark:bg-gray-800/50 border border-gray-200 dark:border-gray-700 rounded-xl p-6 transition-all duration-300 hover:shadow-lg">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-10 h-10 bg-green-100 dark:bg-green-900/30 rounded-lg flex items-center justify-center">
              <MessageSquare className="w-5 h-5 text-green-600 dark:text-green-400" />
            </div>
            <h3 className="font-semibold text-gray-900 dark:text-white">Messages</h3>
          </div>
          <div className="space-y-2">
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Total</span>
              <span className="font-semibold text-gray-900 dark:text-white">{commSummary.messages.total}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Unread</span>
              <span className="font-semibold text-orange-600 dark:text-orange-400">{commSummary.messages.unread}</span>
            </div>
          </div>
        </div>

        {/* Social Media Summary */}
        <div className="bg-white dark:bg-gray-800/50 border border-gray-200 dark:border-gray-700 rounded-xl p-6 transition-all duration-300 hover:shadow-lg">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-10 h-10 bg-purple-100 dark:bg-purple-900/30 rounded-lg flex items-center justify-center">
              <AtSign className="w-5 h-5 text-purple-600 dark:text-purple-400" />
            </div>
            <h3 className="font-semibold text-gray-900 dark:text-white">Social Media</h3>
          </div>
          <div className="space-y-2">
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Mentions</span>
              <span className="font-semibold text-gray-900 dark:text-white">{commSummary.socialMedia.mentions}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600 dark:text-gray-400">Engagement</span>
              <span className="font-semibold text-blue-600 dark:text-blue-400">{commSummary.socialMedia.engagement}</span>
            </div>
          </div>
        </div>
      </div>

      {/* Priority Messages */}
      <div className="bg-white dark:bg-gray-800/50 border border-gray-200 dark:border-gray-700 rounded-xl p-6">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Priority Messages</h3>
        <div className="space-y-3">
          <div className="flex items-center justify-between p-3 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-700/50 rounded-lg">
            <div className="flex items-center gap-3">
              <AlertCircle className="w-5 h-5 text-red-500" />
              <div>
                <p className="font-medium text-gray-900 dark:text-white">Urgent: Project deadline reminder</p>
                <p className="text-sm text-gray-500 dark:text-gray-400">From: project-manager@company.com</p>
              </div>
            </div>
            <span className="text-xs text-red-600 dark:text-red-400">2 min ago</span>
          </div>
          
          <div className="flex items-center justify-between p-3 bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-700/50 rounded-lg">
            <div className="flex items-center gap-3">
              <Mail className="w-5 h-5 text-yellow-500" />
              <div>
                <p className="font-medium text-gray-900 dark:text-white">Meeting reschedule request</p>
                <p className="text-sm text-gray-500 dark:text-gray-400">From: client@example.com</p>
              </div>
            </div>
            <span className="text-xs text-yellow-600 dark:text-yellow-400">15 min ago</span>
          </div>
        </div>
      </div>

      {/* AI Communication Insight */}
      <div className="bg-gradient-to-br from-blue-50 to-indigo-50 dark:from-blue-900/20 dark:to-indigo-900/20 border border-blue-200 dark:border-blue-700/50 rounded-xl p-6">
        <div className="flex items-center gap-2 mb-3">
          <TrendingUp className="w-5 h-5 text-blue-600 dark:text-blue-400" />
          <h3 className="font-semibold text-blue-800 dark:text-blue-300">AI Communication Insight</h3>
        </div>
        {!isPremiumUser ? (
          <p className="text-blue-700 dark:text-blue-400">
            Upgrade to Premium for intelligent message prioritization and communication insights.
          </p>
        ) : isLoadingInsight ? (
          <div className="flex items-center gap-2 text-blue-700 dark:text-blue-400">
            <Loader className="w-4 h-4 animate-spin" />
            <span>Analyzing your communication patterns...</span>
          </div>
        ) : insightError ? (
          <ErrorFallback
            error={insightError}
            resetError={generateCommunicationInsight}
            title="Insight Error"
            description="Could not generate communication insight."
            variant="embedded"
          />
        ) : aiInsight ? (
          <div className="text-blue-800 dark:text-blue-300">
            <TypewriterText text={aiInsight} speed={25} />
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default CommunicationHub;
