import React from 'react';
import { motion } from 'framer-motion';
import { Plus, Target, Heart, DollarSign, Calendar, CheckSquare, MessageSquare } from 'lucide-react';
import type { DashboardWidget } from '../types';

interface WidgetLibraryProps {
  onAddWidget: (widget: Omit<DashboardWidget, 'id'>) => void;
}

const AVAILABLE_WIDGETS = [
  {
    type: 'ai-insights' as const,
    title: 'AI Insights',
    description: 'Intelligent recommendations and priority analysis',
    icon: Target,
    defaultSize: 'large' as const,
    color: 'from-purple-500 to-indigo-600',
  },
  {
    type: 'health-metrics' as const,
    title: 'Health Metrics',
    description: 'Track your wellness and fitness data',
    icon: Heart,
    defaultSize: 'medium' as const,
    color: 'from-red-500 to-pink-600',
  },
  {
    type: 'financial-summary' as const,
    title: 'Financial Summary',
    description: 'Monitor spending, budgets, and financial health',
    icon: DollarSign,
    defaultSize: 'medium' as const,
    color: 'from-green-500 to-emerald-600',
  },
  {
    type: 'calendar-events' as const,
    title: 'Calendar Events',
    description: 'Upcoming appointments and schedule overview',
    icon: Calendar,
    defaultSize: 'small' as const,
    color: 'from-blue-500 to-cyan-600',
  },
  {
    type: 'tasks' as const,
    title: 'Task Management',
    description: 'Track and manage your to-do items',
    icon: CheckSquare,
    defaultSize: 'medium' as const,
    color: 'from-orange-500 to-yellow-600',
  },
  {
    type: 'communication' as const,
    title: 'Communication Hub',
    description: 'Email, messages, and social media overview',
    icon: MessageSquare,
    defaultSize: 'small' as const,
    color: 'from-indigo-500 to-purple-600',
  },
];

const WidgetLibrary: React.FC<WidgetLibraryProps> = ({ onAddWidget }) => {
  const handleAddWidget = (widgetTemplate: typeof AVAILABLE_WIDGETS[0]) => {
    const newWidget: Omit<DashboardWidget, 'id'> = {
      type: widgetTemplate.type,
      title: widgetTemplate.title,
      size: widgetTemplate.defaultSize,
      position: { x: 0, y: 0 }, // Will be auto-positioned
      isVisible: true,
    };
    onAddWidget(newWidget);
  };

  return (
    <div className="space-y-4">
      <h3 className="font-semibold text-gray-900 dark:text-white">Available Widgets</h3>
      <div className="space-y-3">
        {AVAILABLE_WIDGETS.map((widget, index) => {
          const Icon = widget.icon;
          return (
            <motion.div
              key={widget.type}
              className="p-4 border border-gray-200 dark:border-gray-700 rounded-lg hover:border-purple-300 dark:hover:border-purple-600 transition-colors cursor-pointer group"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              onClick={() => handleAddWidget(widget)}
            >
              <div className="flex items-start gap-3">
                <div className={`w-10 h-10 rounded-lg bg-gradient-to-br ${widget.color} flex items-center justify-center flex-shrink-0`}>
                  <Icon className="w-5 h-5 text-white" />
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between">
                    <h4 className="font-medium text-gray-900 dark:text-white group-hover:text-purple-600 dark:group-hover:text-purple-400 transition-colors">
                      {widget.title}
                    </h4>
                    <motion.div
                      className="w-6 h-6 rounded-full bg-purple-100 dark:bg-purple-900/30 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity"
                      whileHover={{ scale: 1.1 }}
                      whileTap={{ scale: 0.9 }}
                    >
                      <Plus className="w-3 h-3 text-purple-600 dark:text-purple-400" />
                    </motion.div>
                  </div>
                  <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                    {widget.description}
                  </p>
                  <div className="flex items-center gap-2 mt-2">
                    <span className="text-xs px-2 py-1 bg-gray-100 dark:bg-gray-800 text-gray-600 dark:text-gray-400 rounded">
                      {widget.defaultSize}
                    </span>
                  </div>
                </div>
              </div>
            </motion.div>
          );
        })}
      </div>
    </div>
  );
};

export default WidgetLibrary;
