import React, { useState, useEffect } from 'react';
import { SubscribeDevProvider, useSubscribeDev } from '@subscribe.dev/react';
import Auth from './components/Auth';
import MainLayout from './components/MainLayout';
import ErrorBoundary from './components/ErrorBoundary';
import AuthErrorBoundary from './components/AuthErrorBoundary';
import { ThemeProvider } from './context/ThemeContext';
import { getEnvironmentConfig, EnvironmentError } from './config/environment';
import type { EnvironmentConfig } from './config/environment';

// Frontend-only version of the app when Subscribe.dev is not available
function FrontendOnlyApp() {
  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 via-white to-indigo-50 dark:from-gray-950 dark:via-purple-950/20 dark:to-gray-950 flex items-center justify-center p-4">
      <div className="max-w-md w-full glass-card p-8 text-center">
        <div className="w-16 h-16 gradient-primary rounded-2xl flex items-center justify-center mx-auto mb-6">
          <div className="w-8 h-8 bg-white rounded-lg"></div>
        </div>
        <h1 className="text-2xl font-bold text-gradient mb-4">Aura (Demo Mode)</h1>
        <p className="text-gray-600 dark:text-gray-400 mb-6 leading-relaxed">
          Running in frontend-only mode. To unlock full functionality, please configure your Subscribe.dev API key.
        </p>
        <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg p-4 text-left">
          <h3 className="text-sm font-semibold text-blue-800 dark:text-blue-200 mb-2">
            To enable full features:
          </h3>
          <ol className="text-sm text-blue-700 dark:text-blue-300 space-y-1">
            <li>1. Get a Subscribe.dev API key</li>
            <li>2. Add it to your .env file as VITE_SUBSCRIBEDEV_PUBLIC_API_KEY</li>
            <li>3. Restart the development server</li>
          </ol>
        </div>
      </div>
    </div>
  );
}

function AgentApp() {
  const { isSignedIn, signIn } = useSubscribeDev();

  if (!isSignedIn) {
    return <Auth signIn={signIn} />;
  }

  return <MainLayout />;
}

function AppWithProvider({ config }: { config: EnvironmentConfig }) {
  const [authError, setAuthError] = useState<Error | null>(null);
  const [retryCount, setRetryCount] = useState(0);
  const [fallbackMode, setFallbackMode] = useState(false);
  const [authErrorCount, setAuthErrorCount] = useState(0);
  const maxRetries = 1; // Reduced retries to fail faster
  const maxAuthErrors = 3; // Maximum auth errors before switching to frontend-only

  // If Subscribe.dev is disabled or we're in fallback mode, show frontend-only version
  if (!config.features.enableSubscribeDev || fallbackMode) {
    return <FrontendOnlyApp />;
  }

  const handleProviderError = (error: Error) => {
    console.error('SubscribeDevProvider error:', error);
    
    // Check if this is an authentication error (SDE008 or similar)
    const isAuthError = error.message?.includes('Invalid Credentials') || 
                       error.message?.includes('SDE008') ||
                       error.message?.includes('Authentication') ||
                       error.name === 'AuthenticationError';
    
    if (isAuthError) {
      const newAuthErrorCount = authErrorCount + 1;
      setAuthErrorCount(newAuthErrorCount);
      
      console.warn(`Subscribe.dev authentication failed (error ${newAuthErrorCount}):`, error.message);
      
      // If we've had too many auth errors, switch to frontend-only mode immediately
      if (newAuthErrorCount >= maxAuthErrors) {
        console.warn('Too many authentication errors, switching to frontend-only mode');
        setFallbackMode(true);
        return;
      }
      
      // For the first few auth errors, try to handle gracefully
      if (retryCount >= maxRetries) {
        console.warn('Max retries reached for Subscribe.dev authentication, switching to frontend-only mode');
        setFallbackMode(true);
        return;
      }
      
      setAuthError(error);
      setRetryCount(prev => prev + 1);
      
      // Don't auto-retry auth errors - let user manually retry
      return;
    }
    
    // Log other errors in production for monitoring
    if (config.features.enableErrorReporting) {
      console.error('Application error:', error);
    }
  };

  // If we have auth errors and exceeded retries, show frontend-only
  if (authError && retryCount >= maxRetries) {
    return <FrontendOnlyApp />;
  }

  return (
    <ThemeProvider>
      <ErrorBoundary
        onError={handleProviderError}
        fallback={authError ? (
          <AuthErrorBoundary 
            error={authError} 
            resetError={() => {
              setAuthError(null);
              setRetryCount(0);
            }}
          />
        ) : undefined}
      >
        <SubscribeDevProvider projectToken={config.subscribeDevApiKey}>
          <AgentApp />
        </SubscribeDevProvider>
      </ErrorBoundary>
    </ThemeProvider>
  );
}

function App() {
  const [config, setConfig] = useState<EnvironmentConfig | null>(null);
  const [error, setError] = useState<Error | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const initializeApp = async () => {
      try {
        setIsLoading(true);
        setError(null);
        
        const environmentConfig = getEnvironmentConfig();
        setConfig(environmentConfig);
        
        // Log environment info in development
        if (environmentConfig.features.enableDebugMode) {
          console.log('🚀 Aura initialized with config:', {
            environment: environmentConfig.environment,
            features: environmentConfig.features,
            apiKeyPresent: !!environmentConfig.subscribeDevApiKey && environmentConfig.subscribeDevApiKey !== 'frontend-only-mode',
            subscribeDevEnabled: environmentConfig.features.enableSubscribeDev,
            mode: environmentConfig.features.enableSubscribeDev ? 'Full Mode' : 'Frontend Only',
          });
          
          if (!environmentConfig.features.enableSubscribeDev) {
            console.warn('⚠️ Running in frontend-only mode. Subscribe.dev features disabled.');
          }
        }
      } catch (err) {
        console.error('Failed to initialize app:', err);
        
        // Always fallback to frontend-only mode instead of showing error
        console.warn('Initialization error, falling back to frontend-only mode');
        try {
          const fallbackConfig = getEnvironmentConfig();
          fallbackConfig.features.enableSubscribeDev = false;
          setConfig(fallbackConfig);
          return;
        } catch (fallbackErr) {
          console.error('Failed to create fallback config:', fallbackErr);
          // Create minimal fallback config
          setConfig({
            subscribeDevApiKey: 'frontend-only-mode',
            environment: 'development',
            features: {
              enableAnalytics: false,
              enableErrorReporting: false,
              enableDebugMode: true,
              enableMockData: true,
              enablePerformanceMonitoring: false,
              enableSubscribeDev: false,
            },
            api: {
              baseUrl: 'https://api.subscribe.dev',
              timeout: 10000,
              retryAttempts: 3,
            },
            security: {
              enableCSP: false,
              allowedOrigins: ['http://localhost:3000', 'http://localhost:5173'],
            },
          });
        }
      } finally {
        setIsLoading(false);
      }
    };

    initializeApp();
  }, []);

  const handleRetry = () => {
    setError(null);
    setConfig(null);
    setIsLoading(true);
    
    // Retry after a short delay
    setTimeout(() => {
      const initializeApp = async () => {
        try {
          const environmentConfig = getEnvironmentConfig();
          setConfig(environmentConfig);
        } catch (err) {
          // Always try frontend-only mode as fallback
          try {
            const fallbackConfig = getEnvironmentConfig();
            fallbackConfig.features.enableSubscribeDev = false;
            setConfig(fallbackConfig);
          } catch (fallbackErr) {
            setError(err instanceof Error ? err : new Error('Unknown initialization error'));
          }
        } finally {
          setIsLoading(false);
        }
      };
      initializeApp();
    }, 100);
  };

  // Loading state
  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-purple-50 via-white to-indigo-50 dark:from-gray-950 dark:via-purple-950/20 dark:to-gray-950 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 gradient-primary rounded-2xl flex items-center justify-center mx-auto mb-4 animate-pulse">
            <div className="w-8 h-8 bg-white rounded-lg"></div>
          </div>
          <h1 className="text-2xl font-bold text-gradient mb-2">Initializing Aura</h1>
          <p className="text-gray-600 dark:text-gray-400">Setting up your AI assistant...</p>
        </div>
      </div>
    );
  }

  // Error state - only show for critical errors that can't be recovered
  if (error) {
    return (
      <AuthErrorBoundary 
        error={error} 
        resetError={handleRetry}
      />
    );
  }

  // Success state
  if (!config) {
    // This should rarely happen now, but provide a fallback
    return <FrontendOnlyApp />;
  }

  return <AppWithProvider config={config} />;
}

export default App;
