import React from 'react';
import { X, CheckCircle, Zap, BrainCircuit } from 'lucide-react';
import type { TransparencyInfo } from '../types';
import { motion, AnimatePresence } from 'framer-motion';
import TypewriterText from './TypewriterText';

interface TransparencyPanelProps {
  info: TransparencyInfo | null;
  onClose: () => void;
}

const TransparencyPanel: React.FC<TransparencyPanelProps> = ({ info, onClose }) => {
  const safeReasoning = Array.isArray(info?.reasoning) ? info.reasoning : ['No reasoning available'];

  const getConfidenceColor = (confidence: number) => {
    if (confidence >= 90) return 'text-green-400';
    if (confidence >= 75) return 'text-blue-400';
    if (confidence >= 60) return 'text-yellow-400';
    return 'text-red-400';
  };

  return (
    <AnimatePresence>
      {info && (
        <motion.div
          className="fixed inset-y-0 right-0 w-full max-w-md bg-white/90 dark:bg-gray-900/90 backdrop-blur-xl border-l border-gray-200 dark:border-gray-700 shadow-2xl z-50"
          initial={{ x: '100%' }}
          animate={{ x: 0 }}
          exit={{ x: '100%' }}
          transition={{ type: 'spring', stiffness: 300, damping: 30 }}
        >
          <div className="flex flex-col h-full">
            <div className="flex items-center justify-between p-6 border-b border-gray-200 dark:border-gray-700">
              <h2 className="text-lg font-semibold flex items-center gap-2 text-gray-900 dark:text-white">
                <BrainCircuit className="w-5 h-5 text-purple-500 dark:text-purple-400" />
                Transparency Report
              </h2>
              <button 
                onClick={onClose} 
                className="text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white transition-colors p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <div className="flex-grow p-6 overflow-y-auto space-y-8">
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.1 }}
              >
                <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">AI Decision</h3>
                <div className="mt-2 text-xl font-semibold text-gray-900 dark:text-white">
                  <TypewriterText text={info.decision || 'No decision available'} speed={20} />
                </div>
              </motion.div>

              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.2 }}
              >
                <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider flex items-center gap-2">
                  <Zap className="w-4 h-4" />
                  Confidence Score
                </h3>
                <div className="flex items-center gap-3 mt-2">
                  <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2.5 overflow-hidden">
                    <motion.div
                      className="bg-gradient-to-r from-purple-500 to-pink-500 h-2.5 rounded-full"
                      initial={{ width: 0 }}
                      animate={{ width: `${info.confidence || 0}%` }}
                      transition={{ duration: 1, ease: "easeOut" }}
                    />
                  </div>
                  <span className={`text-xl font-bold ${getConfidenceColor(info.confidence || 0)}`}>{info.confidence || 0}%</span>
                </div>
              </motion.div>

              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.3 }}
              >
                <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">Evidence Chain</h3>
                <ul className="mt-3 space-y-3">
                  {safeReasoning.map((reason, index) => (
                    <motion.li 
                      key={index} 
                      className="flex items-start gap-3 p-3 bg-gray-100/50 dark:bg-gray-800/50 rounded-lg"
                      initial={{ opacity: 0, x: -20 }}
                      animate={{ opacity: 1, x: 0 }}
                      transition={{ delay: 0.4 + index * 0.1 }}
                    >
                      <CheckCircle className="w-5 h-5 text-green-500 dark:text-green-400 mt-0.5 flex-shrink-0" />
                      <span className="text-gray-700 dark:text-gray-300">{reason}</span>
                    </motion.li>
                  ))}
                </ul>
              </motion.div>
            </div>

            <motion.div 
              className="p-6 border-t border-gray-200 dark:border-gray-700 text-center"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.5 }}
            >
              <p className="text-xs text-gray-500">Aura's decisions are auditable to ensure user trust.</p>
            </motion.div>
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};

export default TransparencyPanel;
