import React from 'react';
import { AlertTriangle, RefreshCw, ArrowLeft } from 'lucide-react';
import { motion } from 'framer-motion';

interface ErrorFallbackProps {
  error?: Error;
  resetError?: () => void;
  title?: string;
  description?: string;
  showRetry?: boolean;
  showBack?: boolean;
  onBack?: () => void;
  variant?: 'full' | 'embedded';
}

const ErrorFallback: React.FC<ErrorFallbackProps> = ({
  error,
  resetError,
  title = "Something went wrong",
  description = "We encountered an unexpected error. Please try again.",
  showRetry = true,
  showBack = false,
  onBack,
  variant = 'full',
}) => {
  if (variant === 'embedded') {
    return (
      <motion.div
        className="flex flex-col items-center justify-center p-4 text-center bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-xl"
        initial={{ opacity: 0, y: 10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <AlertTriangle className="w-6 h-6 text-red-500 mb-2" />
        <h2 className="text-md font-bold text-gray-900 dark:text-white mb-1">
          {title}
        </h2>
        <p className="text-sm text-gray-600 dark:text-gray-400 mb-4 max-w-md">
          {description}
        </p>
        {showRetry && resetError && (
          <motion.button
            onClick={resetError}
            className="btn-secondary flex items-center gap-2 text-sm py-1 px-3"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <RefreshCw className="w-3 h-3" />
            Try Again
          </motion.button>
        )}
      </motion.div>
    );
  }

  return (
    <motion.div 
      className="flex flex-col items-center justify-center min-h-[400px] p-8 text-center"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <motion.div 
        className="w-16 h-16 bg-red-100 dark:bg-red-900/30 rounded-2xl flex items-center justify-center mb-6"
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        transition={{ delay: 0.2, type: "spring", stiffness: 500, damping: 15 }}
      >
        <AlertTriangle className="w-8 h-8 text-red-500" />
      </motion.div>
      
      <h2 className="text-xl font-bold text-gray-900 dark:text-white mb-3">
        {title}
      </h2>
      
      <p className="text-gray-600 dark:text-gray-400 mb-6 max-w-md leading-relaxed">
        {description}
      </p>

      {import.meta.env.DEV && error && (
        <motion.div 
          className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4 mb-6 max-w-md w-full"
          initial={{ opacity: 0, height: 0 }}
          animate={{ opacity: 1, height: 'auto' }}
          transition={{ delay: 0.3 }}
        >
          <h3 className="text-sm font-semibold text-red-800 dark:text-red-200 mb-2">
            Error Details (Development Mode)
          </h3>
          <pre className="text-xs text-red-700 dark:text-red-300 overflow-auto max-h-32 text-left">
            {error.message}
          </pre>
        </motion.div>
      )}

      <div className="flex gap-3">
        {showBack && onBack && (
          <motion.button
            onClick={onBack}
            className="btn-secondary flex items-center gap-2"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <ArrowLeft className="w-4 h-4" />
            Go Back
          </motion.button>
        )}
        
        {showRetry && resetError && (
          <motion.button
            onClick={resetError}
            className="btn-primary flex items-center gap-2"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <RefreshCw className="w-4 h-4" />
            Try Again
          </motion.button>
        )}
      </div>
    </motion.div>
  );
};

export default ErrorFallback;
