import { useState, useCallback } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import type { Integration, IntegrationConfig, OAuthState } from '../types';

// Separate mock data from integration logic
const MOCK_INTEGRATIONS: Integration[] = [
  {
    id: 'plaid',
    name: 'Plaid Banking',
    type: 'financial',
    connected: false,
    status: 'disconnected',
    requiredTier: 'pro',
  },
  {
    id: 'gmail',
    name: 'Gmail',
    type: 'communication',
    connected: false,
    status: 'disconnected',
    requiredTier: 'pro',
  },
  {
    id: 'fitbit',
    name: 'Fitbit',
    type: 'health',
    connected: false,
    status: 'disconnected',
    requiredTier: 'pro',
  },
  {
    id: 'twitter',
    name: 'Twitter/X',
    type: 'communication',
    connected: false,
    status: 'disconnected',
    requiredTier: 'enterprise',
  },
  {
    id: 'apple-health',
    name: 'Apple Health',
    type: 'health',
    connected: false,
    status: 'disconnected',
    requiredTier: 'free',
  },
  {
    id: 'slack',
    name: 'Slack',
    type: 'communication',
    connected: false,
    status: 'disconnected',
    requiredTier: 'enterprise',
  },
];

// Mock data generators
const generateMockDataPreview = (integrationId: string) => {
  switch (integrationId) {
    case 'plaid':
      return {
        accounts: Math.floor(Math.random() * 3) + 1,
        lastTransaction: 'Starbucks Coffee - $4.50',
        totalBalance: `$${(Math.random() * 10000 + 1000).toFixed(2)}`,
      };
    case 'gmail':
      return {
        unreadEmails: Math.floor(Math.random() * 50) + 5,
        urgentEmails: Math.floor(Math.random() * 10) + 1,
        lastEmail: 'Project Update - Team Meeting',
      };
    case 'fitbit':
      return {
        todaySteps: `${(Math.random() * 5000 + 5000).toFixed(0)} steps`,
        lastNightSleep: `${(Math.random() * 2 + 6).toFixed(1)} hours`,
        heartRate: `${Math.floor(Math.random() * 20 + 60)} bpm`,
      };
    case 'twitter':
      return {
        mentions: Math.floor(Math.random() * 50) + 10,
        engagement: Math.floor(Math.random() * 200) + 50,
        lastMention: '@user_handle great work on the project!',
      };
    default:
      return {};
  }
};

export const useIntegrations = () => {
  const { useStorage } = useSubscribeDev();
  const [integrations, setIntegrations] = useStorage<Integration[]>('user_integrations', MOCK_INTEGRATIONS);
  const [oauthStates, setOauthStates] = useStorage<OAuthState[]>('oauth_states', []);
  const [isConnecting, setIsConnecting] = useState<string | null>(null);

  const generateState = useCallback(() => {
    return btoa(crypto.getRandomValues(new Uint8Array(32)).toString());
  }, []);

  const simulateConnection = useCallback(async (integrationId: string): Promise<void> => {
    // Simulate network delay
    await new Promise(resolve => setTimeout(resolve, 2000 + Math.random() * 1000));
    
    // Simulate occasional failures for testing
    if (Math.random() < 0.1) {
      throw new Error('Connection failed due to network timeout');
    }
  }, []);

  const connectIntegration = useCallback(async (integrationId: string) => {
    const integration = integrations.find(i => i.id === integrationId);
    if (!integration) {
      throw new Error(`Integration ${integrationId} not found`);
    }

    setIsConnecting(integrationId);

    try {
      // Update status to authenticating
      const authenticatingIntegrations = integrations.map(integration => 
        integration.id === integrationId
          ? { ...integration, status: 'authenticating' as const, error: undefined }
          : integration
      );
      setIntegrations(authenticatingIntegrations);

      // Simulate connection process
      await simulateConnection(integrationId);

      // Generate mock configuration and data preview
      const mockConfig: IntegrationConfig = {
        [integrationId]: {
          accessToken: `mock_${integrationId}_access_token_${Date.now()}`,
          refreshToken: `mock_${integrationId}_refresh_token_${Date.now()}`,
          ...(integrationId === 'gmail' && { email: 'user@example.com' }),
          ...(integrationId === 'fitbit' && { userId: 'mock_user_id' }),
          ...(integrationId === 'twitter' && { username: 'user_handle' }),
          ...(integrationId === 'plaid' && { 
            itemId: 'mock_item_id',
            institutionId: 'mock_institution_id',
            institutionName: 'Mock Bank'
          }),
        }
      };

      const dataPreview = generateMockDataPreview(integrationId);

      // Update to connected state
      const connectedIntegrations = integrations.map(integration => 
        integration.id === integrationId
          ? {
              ...integration,
              connected: true,
              status: 'connected' as const,
              lastSync: new Date(),
              config: mockConfig,
              dataPreview,
              error: undefined,
            }
          : integration
      );
      setIntegrations(connectedIntegrations);

    } catch (error) {
      const errorIntegrations = integrations.map(integration => 
        integration.id === integrationId
          ? {
              ...integration,
              status: 'error' as const,
              error: error instanceof Error ? error.message : 'Connection failed',
            }
          : integration
      );
      setIntegrations(errorIntegrations);
      throw error;
    } finally {
      setIsConnecting(null);
    }
  }, [integrations, setIntegrations, simulateConnection]);

  const disconnectIntegration = useCallback(async (integrationId: string) => {
    const updatedIntegrations = integrations.map(integration => 
      integration.id === integrationId
        ? {
            ...integration,
            connected: false,
            status: 'disconnected' as const,
            lastSync: undefined,
            config: undefined,
            dataPreview: undefined,
            error: undefined,
          }
        : integration
    );
    setIntegrations(updatedIntegrations);
  }, [integrations, setIntegrations]);

  const syncIntegration = useCallback(async (integrationId: string) => {
    const integration = integrations.find(i => i.id === integrationId);
    if (!integration || !integration.connected) {
      throw new Error('Integration not connected');
    }

    const syncingIntegrations = integrations.map(integration => 
      integration.id === integrationId
        ? { ...integration, status: 'syncing' as const }
        : integration
    );
    setIntegrations(syncingIntegrations);

    try {
      // Simulate sync process
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // Update data preview with fresh mock data
      const freshDataPreview = generateMockDataPreview(integrationId);

      const syncedIntegrations = integrations.map(integration => 
        integration.id === integrationId
          ? {
              ...integration,
              status: 'connected' as const,
              lastSync: new Date(),
              dataPreview: freshDataPreview,
            }
          : integration
      );
      setIntegrations(syncedIntegrations);
    } catch (error) {
      const errorIntegrations = integrations.map(integration => 
        integration.id === integrationId
          ? {
              ...integration,
              status: 'error' as const,
              error: error instanceof Error ? error.message : 'Sync failed',
            }
          : integration
      );
      setIntegrations(errorIntegrations);
      throw error;
    }
  }, [integrations, setIntegrations]);

  const retryConnection = useCallback(async (integrationId: string) => {
    return connectIntegration(integrationId);
  }, [connectIntegration]);

  return {
    integrations,
    isConnecting,
    connectIntegration,
    disconnectIntegration,
    syncIntegration,
    retryConnection,
  };
};
