import React from 'react';
import { AlertTriangle, RefreshCw, Shield, Mail } from 'lucide-react';
import { motion } from 'framer-motion';
import type { EnvironmentError } from '../config/environment';

interface AuthErrorBoundaryProps {
  error: Error | EnvironmentError;
  resetError: () => void;
}

const AuthErrorBoundary: React.FC<AuthErrorBoundaryProps> = ({ error, resetError }) => {
  const isEnvironmentError = error instanceof Error && error.name === 'EnvironmentError';
  const environmentError = error as EnvironmentError;

  if (isEnvironmentError) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-red-50 via-white to-orange-50 dark:from-gray-950 dark:via-red-950/20 dark:to-gray-950 flex items-center justify-center p-4">
        <motion.div 
          className="max-w-lg w-full glass-card p-8"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <motion.div 
            className="w-16 h-16 bg-red-100 dark:bg-red-900/30 rounded-2xl flex items-center justify-center mx-auto mb-6"
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ delay: 0.2, type: "spring", stiffness: 500, damping: 15 }}
          >
            <Shield className="w-8 h-8 text-red-500" />
          </motion.div>
          
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-3 text-center">
            Configuration Error
          </h1>
          
          <p className="text-gray-600 dark:text-gray-400 mb-6 text-center leading-relaxed">
            The application is missing required environment variables and cannot start.
          </p>

          <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4 mb-6">
            <h3 className="text-sm font-semibold text-red-800 dark:text-red-200 mb-3">
              Missing Environment Variables:
            </h3>
            <ul className="space-y-1">
              {environmentError.missingKeys?.map((key) => (
                <li key={key} className="text-sm text-red-700 dark:text-red-300 font-mono">
                  • {key}
                </li>
              ))}
            </ul>
          </div>

          <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg p-4 mb-6">
            <h3 className="text-sm font-semibold text-blue-800 dark:text-blue-200 mb-2">
              How to fix this:
            </h3>
            <ol className="text-sm text-blue-700 dark:text-blue-300 space-y-1">
              <li>1. Create a <code className="bg-blue-100 dark:bg-blue-900/50 px-1 rounded">.env</code> file in your project root</li>
              <li>2. Add the missing environment variables</li>
              <li>3. Restart the development server</li>
            </ol>
          </div>

          <div className="space-y-3">
            <motion.button
              onClick={resetError}
              className="w-full btn-primary flex items-center justify-center gap-2"
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
            >
              <RefreshCw className="w-4 h-4" />
              Retry
            </motion.button>
          </div>

          <div className="mt-8 pt-6 border-t border-gray-200 dark:border-gray-700 text-center">
            <p className="text-xs text-gray-500 dark:text-gray-400 mb-3">
              Need help setting up your environment?
            </p>
            <a 
              href="mailto:support@aura.ai" 
              className="inline-flex items-center gap-2 text-sm text-purple-600 dark:text-purple-400 hover:text-purple-700 dark:hover:text-purple-300 transition-colors"
            >
              <Mail className="w-4 h-4" />
              Contact Support
            </a>
          </div>
        </motion.div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-50 via-white to-orange-50 dark:from-gray-950 dark:via-red-950/20 dark:to-gray-950 flex items-center justify-center p-4">
      <motion.div 
        className="max-w-md w-full glass-card p-8 text-center"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <motion.div 
          className="w-16 h-16 bg-red-100 dark:bg-red-900/30 rounded-2xl flex items-center justify-center mx-auto mb-6"
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          transition={{ delay: 0.2, type: "spring", stiffness: 500, damping: 15 }}
        >
          <AlertTriangle className="w-8 h-8 text-red-500" />
        </motion.div>
        
        <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-3">
          Authentication Error
        </h1>
        
        <p className="text-gray-600 dark:text-gray-400 mb-6 leading-relaxed">
          We encountered an error while setting up authentication. Please try again.
        </p>

        {import.meta.env.DEV && (
          <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4 mb-6 text-left">
            <h3 className="text-sm font-semibold text-red-800 dark:text-red-200 mb-2">
              Error Details (Development Mode)
            </h3>
            <pre className="text-xs text-red-700 dark:text-red-300 overflow-auto max-h-32">
              {error.message}
            </pre>
          </div>
        )}

        <motion.button
          onClick={resetError}
          className="w-full btn-primary flex items-center justify-center gap-2"
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          <RefreshCw className="w-4 h-4" />
          Try Again
        </motion.button>
      </motion.div>
    </div>
  );
};

export default AuthErrorBoundary;
