import React from 'react';
import { motion } from 'framer-motion';

interface SkeletonLoaderProps {
  variant?: 'card' | 'text' | 'avatar' | 'button' | 'chart';
  width?: string | number;
  height?: string | number;
  className?: string;
  count?: number;
}

const SkeletonLoader: React.FC<SkeletonLoaderProps> = ({
  variant = 'text',
  width,
  height,
  className = '',
  count = 1,
}) => {
  const getSkeletonStyle = () => {
    switch (variant) {
      case 'card':
        return 'w-full h-48 rounded-xl';
      case 'avatar':
        return 'w-10 h-10 rounded-full';
      case 'button':
        return 'w-24 h-10 rounded-lg';
      case 'chart':
        return 'w-full h-64 rounded-lg';
      default:
        return 'w-full h-4 rounded';
    }
  };

  const skeletonClass = `${getSkeletonStyle()} bg-gradient-to-r from-gray-200 via-gray-300 to-gray-200 dark:from-gray-700 dark:via-gray-600 dark:to-gray-700 ${className}`;

  const customStyle = {
    width: width || undefined,
    height: height || undefined,
    backgroundSize: '200% 100%',
  };

  return (
    <div className="space-y-3">
      {Array.from({ length: count }).map((_, index) => (
        <motion.div
          key={index}
          className={skeletonClass}
          style={customStyle}
          animate={{
            backgroundPosition: ['200% 0', '-200% 0'],
          }}
          transition={{
            duration: 1.5,
            repeat: Infinity,
            ease: 'linear',
            delay: index * 0.1,
          }}
        />
      ))}
    </div>
  );
};

export default React.memo(SkeletonLoader);
