import React, { useState } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { Check, AlertCircle, Loader, Lock, ExternalLink, RefreshCw, Zap, Shield } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import type { Integration } from '../types';
import { useIntegrations } from '../hooks/useIntegrations';
import IntegrationModal from './IntegrationModal';
import DataPreview from './DataPreview';

interface IntegrationCardProps {
  integration: Integration;
}

const IntegrationCard: React.FC<IntegrationCardProps> = ({ integration }) => {
  const { subscriptionStatus, subscribe } = useSubscribeDev();
  const { connectIntegration, disconnectIntegration, syncIntegration, isConnecting } = useIntegrations();
  const [showModal, setShowModal] = useState(false);

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;
  const canConnect = integration.requiredTier === 'free' || 
    (integration.requiredTier === 'pro' && isPremiumUser) ||
    (integration.requiredTier === 'enterprise' && isPremiumUser);

  const isCurrentlyConnecting = isConnecting === integration.id;

  const handleConnect = async () => {
    if (!canConnect) {
      subscribe();
      return;
    }
    setShowModal(true);
  };

  const handleModalConnect = async (integrationId: string) => {
    await connectIntegration(integrationId);
    setShowModal(false);
  };

  const handleDisconnect = async () => {
    await disconnectIntegration(integration.id);
  };

  const handleSync = async () => {
    await syncIntegration(integration.id);
  };

  const getStatusIcon = () => {
    if (isCurrentlyConnecting || integration.status === 'syncing' || integration.status === 'authenticating') {
      return <Loader className="w-5 h-5 animate-spin text-blue-500" />;
    }
    if (integration.status === 'connected') {
      return <Check className="w-5 h-5 text-green-500" />;
    }
    if (integration.status === 'error') {
      return <AlertCircle className="w-5 h-5 text-red-500" />;
    }
    if (!canConnect) {
      return <Lock className="w-5 h-5 text-gray-400" />;
    }
    return null;
  };

  const getStatusText = () => {
    if (integration.status === 'connected') return 'Connected';
    if (integration.status === 'error') return 'Connection Error';
    if (integration.status === 'syncing') return 'Syncing...';
    if (integration.status === 'authenticating') return 'Authenticating...';
    if (!canConnect) return `${integration.requiredTier} Required`;
    return 'Not Connected';
  };

  const getTypeColor = () => {
    switch (integration.type) {
      case 'financial': return 'from-green-500 to-emerald-600';
      case 'health': return 'from-red-500 to-pink-600';
      case 'communication': return 'from-blue-500 to-indigo-600';
      default: return 'from-gray-500 to-gray-600';
    }
  };

  const getStatusColor = () => {
    switch (integration.status) {
      case 'connected': return 'border-green-200 dark:border-green-800 bg-green-50 dark:bg-green-900/20';
      case 'error': return 'border-red-200 dark:border-red-800 bg-red-50 dark:bg-red-900/20';
      case 'syncing': 
      case 'authenticating': return 'border-blue-200 dark:border-blue-800 bg-blue-50 dark:bg-blue-900/20';
      default: return 'border-gray-200 dark:border-gray-700 bg-white dark:bg-gray-800/50';
    }
  };

  return (
    <>
      <motion.div 
        className={`border rounded-xl p-6 transition-all duration-300 hover:shadow-lg hover:scale-[1.02] ${getStatusColor()}`}
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        whileHover={{ y: -2 }}
      >
        <div className="flex items-start justify-between mb-4">
          <div className="flex items-center gap-3">
            <div className={`w-12 h-12 rounded-xl bg-gradient-to-br ${getTypeColor()} flex items-center justify-center shadow-lg relative`}>
              <ExternalLink className="w-6 h-6 text-white" />
              {integration.connected && (
                <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white dark:border-gray-800 flex items-center justify-center">
                  <Check className="w-2 h-2 text-white" />
                </div>
              )}
            </div>
            <div>
              <h3 className="font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                {integration.name}
                {integration.requiredTier !== 'free' && (
                  <div className="flex items-center gap-1 px-2 py-1 bg-purple-100 dark:bg-purple-900/30 rounded-full">
                    <Zap className="w-3 h-3 text-purple-600 dark:text-purple-400" />
                    <span className="text-xs font-medium text-purple-600 dark:text-purple-400 capitalize">
                      {integration.requiredTier}
                    </span>
                  </div>
                )}
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 capitalize">{integration.type}</p>
            </div>
          </div>
          <div className="flex items-center gap-2">
            {getStatusIcon()}
            {integration.connected && (
              <motion.button
                onClick={handleSync}
                className="p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                disabled={integration.status === 'syncing'}
                title="Sync data"
              >
                <RefreshCw className={`w-4 h-4 text-gray-500 ${integration.status === 'syncing' ? 'animate-spin' : ''}`} />
              </motion.button>
            )}
          </div>
        </div>

        <div className="mb-4">
          <div className="flex items-center justify-between mb-2">
            <span className="text-sm text-gray-500 dark:text-gray-400">Status</span>
            <span className={`text-sm font-medium ${
              integration.status === 'connected' ? 'text-green-600 dark:text-green-400' :
              integration.status === 'error' ? 'text-red-600 dark:text-red-400' :
              integration.status === 'syncing' || integration.status === 'authenticating' ? 'text-blue-600 dark:text-blue-400' :
              'text-gray-600 dark:text-gray-400'
            }`}>
              {getStatusText()}
            </span>
          </div>
          
          {integration.error && (
            <div className="mt-2 p-2 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg">
              <p className="text-xs text-red-700 dark:text-red-300">{integration.error}</p>
            </div>
          )}
          
          {integration.lastSync && (
            <p className="text-xs text-gray-400 mt-1">
              Last sync: {integration.lastSync.toLocaleDateString()} at {integration.lastSync.toLocaleTimeString()}
            </p>
          )}
        </div>

        {/* Data Preview */}
        <AnimatePresence>
          {integration.connected && integration.dataPreview && (
            <DataPreview integration={integration} />
          )}
        </AnimatePresence>

        <div className="space-y-2 mt-4">
          {integration.connected ? (
            <div className="flex gap-2">
              <motion.button
                onClick={handleDisconnect}
                disabled={isCurrentlyConnecting}
                className="flex-1 bg-red-100 dark:bg-red-900/20 hover:bg-red-200 dark:hover:bg-red-900/30 text-red-700 dark:text-red-400 font-medium py-2 px-4 rounded-lg transition-colors disabled:opacity-50"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                Disconnect
              </motion.button>
              <motion.button
                onClick={() => setShowModal(true)}
                className="px-4 py-2 bg-gray-100 dark:bg-gray-700 hover:bg-gray-200 dark:hover:bg-gray-600 text-gray-700 dark:text-gray-300 rounded-lg transition-colors"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                title="View details"
              >
                <ExternalLink className="w-4 h-4" />
              </motion.button>
            </div>
          ) : (
            <motion.button
              onClick={handleConnect}
              disabled={isCurrentlyConnecting}
              className={`w-full font-medium py-3 px-4 rounded-lg transition-all duration-200 disabled:opacity-50 flex items-center justify-center gap-2 ${
                canConnect
                  ? 'bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-500 hover:to-purple-600 text-white transform hover:scale-[1.02]'
                  : 'bg-gray-200 dark:bg-gray-700 text-gray-500 dark:text-gray-400 cursor-not-allowed'
              }`}
              whileHover={canConnect ? { scale: 1.02 } : {}}
              whileTap={canConnect ? { scale: 0.98 } : {}}
            >
              {isCurrentlyConnecting ? (
                <>
                  <Loader className="w-4 h-4 animate-spin" />
                  Connecting...
                </>
              ) : canConnect ? (
                <>
                  <Shield className="w-4 h-4" />
                  Connect Securely
                </>
              ) : (
                <>
                  <Lock className="w-4 h-4" />
                  Upgrade Required
                </>
              )}
            </motion.button>
          )}
          
          {!canConnect && (
            <p className="text-xs text-center text-gray-500 dark:text-gray-400">
              Requires {integration.requiredTier} plan
            </p>
          )}
        </div>
      </motion.div>

      <IntegrationModal
        integration={integration}
        isOpen={showModal}
        onClose={() => setShowModal(false)}
        onConnect={handleModalConnect}
        isConnecting={isCurrentlyConnecting}
      />
    </>
  );
};

export default IntegrationCard;
