export const useDashboardLayout = () => {
  const { useStorage } = useSubscribeDev();
  const [layouts, setLayouts] = useStorage<DashboardLayout[]>('dashboard_layouts', DEFAULT_LAYOUTS);
  const [currentLayoutId, setCurrentLayoutId] = useStorage<string>('current_layout_id', 'default');
  const [isCustomizing, setIsCustomizing] = useState(false);

  const currentLayout = layouts.find(layout => layout.id === currentLayoutId) || layouts[0];

  const updateWidget = useCallback((widgetId: string, updates: Partial<DashboardWidget>) => {
    const updatedLayouts = layouts.map(layout => {
      if (layout.id === currentLayoutId) {
        return {
          ...layout,
          widgets: layout.widgets.map(widget =>
            widget.id === widgetId ? { ...widget, ...updates } : widget
          ),
          lastModified: new Date(),
        };
      }
      return layout;
    });
    setLayouts(updatedLayouts);
  }, [layouts, currentLayoutId, setLayouts]);

  const addWidget = useCallback((widget: Omit<DashboardWidget, 'id'>) => {
    const newWidget: DashboardWidget = {
      ...widget,
      id: `${widget.type}-${Date.now()}`,
    };

    const updatedLayouts = layouts.map(layout => {
      if (layout.id === currentLayoutId) {
        return {
          ...layout,
          widgets: [...layout.widgets, newWidget],
          lastModified: new Date(),
        };
      }
      return layout;
    });
    setLayouts(updatedLayouts);
  }, [layouts, currentLayoutId, setLayouts]);

  const removeWidget = useCallback((widgetId: string) => {
    const updatedLayouts = layouts.map(layout => {
      if (layout.id === currentLayoutId) {
        return {
          ...layout,
          widgets: layout.widgets.filter(widget => widget.id !== widgetId),
          lastModified: new Date(),
        };
      }
      return layout;
    });
    setLayouts(updatedLayouts);
  }, [layouts, currentLayoutId, setLayouts]);

  const createLayout = useCallback((name: string, basedOn?: string) => {
    const baseLayout = basedOn ? layouts.find(l => l.id === basedOn) : layouts[0];
    const newLayout: DashboardLayout = {
      id: `layout-${Date.now()}`,
      name,
      isDefault: false,
      createdAt: new Date(),
      lastModified: new Date(),
      widgets: baseLayout ? [...baseLayout.widgets] : [],
    };

    setLayouts([...layouts, newLayout]);
    setCurrentLayoutId(newLayout.id);
  }, [layouts, setLayouts, setCurrentLayoutId]);

  const deleteLayout = useCallback((layoutId: string) => {
    if (layouts.length <= 1) return; // Don't delete the last layout
    
    const updatedLayouts = layouts.filter(layout => layout.id !== layoutId);
    setLayouts(updatedLayouts);
    
    if (currentLayoutId === layoutId) {
      setCurrentLayoutId(updatedLayouts[0].id);
    }
  }, [layouts, currentLayoutId, setLayouts, setCurrentLayoutId]);

  const switchLayout = useCallback((layoutId: string) => {
    setCurrentLayoutId(layoutId);
  }, [setCurrentLayoutId]);

  const resetToDefault = useCallback(() => {
    setLayouts(DEFAULT_LAYOUTS);
    setCurrentLayoutId('default');
  }, [setLayouts, setCurrentLayoutId]);

  return {
    layouts,
    currentLayout,
    currentLayoutId,
    isCustomizing,
    setIsCustomizing,
    updateWidget,
    addWidget,
    removeWidget,
    createLayout,
    deleteLayout,
    switchLayout,
    resetToDefault,
  };
};
