import React, { useState, useEffect } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { DollarSign, TrendingUp, TrendingDown, CreditCard, PiggyBank, Loader, AlertTriangle, Target, ArrowUpRight, ArrowDownRight, Calendar, Zap } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import type { BankAccount, Transaction, SpendingCategory } from '../types';
import TypewriterText from './TypewriterText';
import ErrorToast from './ErrorToast';
import SkeletonLoader from './SkeletonLoader';
import VirtualizedTransactionList from './VirtualizedTransactionList';

const FinancialDashboard: React.FC = React.memo(() => {
  const { client, useStorage, subscriptionStatus } = useSubscribeDev();
  
  const [accounts] = useStorage<BankAccount[]>('bank_accounts', [
    {
      id: '1',
      name: 'Main Checking',
      type: 'checking',
      balance: 4250.75,
      lastUpdated: new Date(),
      institution: 'Chase Bank'
    },
    {
      id: '2',
      name: 'Savings Account',
      type: 'savings',
      balance: 12800.00,
      lastUpdated: new Date(),
      institution: 'Chase Bank'
    },
    {
      id: '3',
      name: 'Credit Card',
      type: 'credit',
      balance: -1250.30,
      lastUpdated: new Date(),
      institution: 'American Express'
    }
  ]);

  const [spendingCategories] = useStorage<SpendingCategory[]>('spending_categories', [
    { name: 'Food & Dining', amount: 850, percentage: 35, color: 'bg-red-500' },
    { name: 'Transportation', amount: 420, percentage: 17, color: 'bg-blue-500' },
    { name: 'Shopping', amount: 380, percentage: 16, color: 'bg-purple-500' },
    { name: 'Bills & Utilities', amount: 320, percentage: 13, color: 'bg-green-500' },
    { name: 'Entertainment', amount: 280, percentage: 12, color: 'bg-yellow-500' },
    { name: 'Other', amount: 170, percentage: 7, color: 'bg-gray-500' }
  ]);

  const [recentTransactions] = useStorage<Transaction[]>('recent_transactions', [
    { id: '1', amount: -45.67, description: 'Whole Foods Market', category: 'Food & Dining', date: new Date(), accountId: '1' },
    { id: '2', amount: -12.50, description: 'Spotify Premium', category: 'Entertainment', date: new Date(Date.now() - 86400000), accountId: '1' },
    { id: '3', amount: 2500.00, description: 'Salary Deposit', category: 'Income', date: new Date(Date.now() - 172800000), accountId: '1' },
    { id: '4', amount: -89.23, description: 'Shell Gas Station', category: 'Transportation', date: new Date(Date.now() - 259200000), accountId: '1' },
    { id: '5', amount: -156.78, description: 'Amazon Purchase', category: 'Shopping', date: new Date(Date.now() - 345600000), accountId: '1' },
    // Add more transactions for virtualization demo
    ...Array.from({ length: 100 }, (_, i) => ({
      id: `${i + 6}`,
      amount: Math.random() > 0.7 ? Math.random() * 1000 : -(Math.random() * 200),
      description: `Transaction ${i + 6}`,
      category: ['Food & Dining', 'Transportation', 'Shopping', 'Entertainment'][Math.floor(Math.random() * 4)],
      date: new Date(Date.now() - i * 86400000),
      accountId: '1'
    }))
  ]);

  const [aiInsight, setAiInsight] = useState<string>('');
  const [isLoadingInsight, setIsLoadingInsight] = useState(false);
  const [insightError, setInsightError] = useState<{
    type: string;
    message: string;
    code?: string;
  } | null>(null);
  const [selectedTimeframe, setSelectedTimeframe] = useState<'week' | 'month' | 'quarter'>('month');

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;
  const totalBalance = accounts.reduce((sum, account) => sum + account.balance, 0);
  const totalSpending = spendingCategories.reduce((sum, cat) => sum + cat.amount, 0);
  const monthlyIncome = 2500;
  const savingsRate = ((monthlyIncome - totalSpending) / monthlyIncome) * 100;

  const generateFinancialInsight = async () => {
    if (!client || !isPremiumUser) return;

    setIsLoadingInsight(true);
    setInsightError(null);
    try {
      const topSpendingCategory = spendingCategories[0];
      const creditUtilization = Math.abs(accounts.find(a => a.type === 'credit')?.balance || 0);
      
      const response = await client.run('openai/gpt-4o', {
        input: {
          messages: [{
            role: 'user',
            content: `As a financial advisor AI, analyze this financial data and provide a specific, actionable insight:
            
            Financial Overview:
            - Total Balance: $${totalBalance.toFixed(2)}
            - Monthly Income: $${monthlyIncome}
            - Monthly Spending: $${totalSpending}
            - Savings Rate: ${savingsRate.toFixed(1)}%
            - Credit Card Debt: $${creditUtilization}
            
            Spending Breakdown:
            - Top Category: ${topSpendingCategory.name} ($${topSpendingCategory.amount}, ${topSpendingCategory.percentage}%)
            - Other Categories: ${spendingCategories.slice(1, 3).map(c => `${c.name} ($${c.amount})`).join(', ')}
            
            Recent Activity:
            ${recentTransactions.slice(0, 3).map(t => `- ${t.description}: $${t.amount}`).join('\n')}
            
            Provide ONE specific recommendation in 1-2 sentences to improve their financial health. Focus on the most impactful action they can take this week.`
          }]
        }
      });

      setAiInsight(response.output[0] as string);
    } catch (error: any) {
      setInsightError({
        type: error.type || 'unknown_error',
        message: error.message || 'Could not generate financial insight.',
        code: error.code,
      });
    } finally {
      setIsLoadingInsight(false);
    }
  };

  useEffect(() => {
    if (isPremiumUser) {
      generateFinancialInsight();
    }
  }, [isPremiumUser, selectedTimeframe]);

  const getAccountIcon = (type: string) => {
    switch (type) {
      case 'credit': return CreditCard;
      case 'savings': return PiggyBank;
      default: return DollarSign;
    }
  };

  const getAccountColor = (type: string) => {
    switch (type) {
      case 'credit': return 'from-purple-500 to-pink-600';
      case 'savings': return 'from-green-500 to-emerald-600';
      default: return 'from-blue-500 to-indigo-600';
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 2
    }).format(Math.abs(amount));
  };

  return (
    <div className="space-y-8">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white">Financial Overview</h2>
          <p className="text-gray-500 dark:text-gray-400 mt-1">Track your spending, savings, and financial health</p>
        </div>
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2 text-sm text-gray-500 dark:text-gray-400">
            <Calendar className="w-4 h-4" />
            <span>Last updated: {new Date().toLocaleDateString()}</span>
          </div>
          <select 
            value={selectedTimeframe}
            onChange={(e) => setSelectedTimeframe(e.target.value as any)}
            className="glass px-3 py-2 rounded-lg text-sm border border-gray-300 dark:border-gray-600 focus:outline-none focus:ring-2 focus:ring-purple-500"
          >
            <option value="week">This Week</option>
            <option value="month">This Month</option>
            <option value="quarter">This Quarter</option>
          </select>
        </div>
      </div>

      {/* Financial Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <motion.div 
          className="glass-card p-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
        >
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-gradient-to-br from-green-500 to-emerald-600 rounded-xl flex items-center justify-center">
              <TrendingUp className="w-6 h-6 text-white" />
            </div>
            <span className="text-xs text-green-600 dark:text-green-400 font-semibold">+2.3%</span>
          </div>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">Net Worth</h3>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">{formatCurrency(totalBalance)}</p>
        </motion.div>

        <motion.div 
          className="glass-card p-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
        >
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-indigo-600 rounded-xl flex items-center justify-center">
              <DollarSign className="w-6 h-6 text-white" />
            </div>
            <span className="text-xs text-blue-600 dark:text-blue-400 font-semibold">Monthly</span>
          </div>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">Income</h3>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">{formatCurrency(monthlyIncome)}</p>
        </motion.div>

        <motion.div 
          className="glass-card p-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
        >
          <div className="flex items-center justify-between mb-4">
            <div className="w-12 h-12 bg-gradient-to-br from-red-500 to-pink-600 rounded-xl flex items-center justify-center">
              <TrendingDown className="w-6 h-6 text-white" />
            </div>
            <span className="text-xs text-red-600 dark:text-red-400 font-semibold">-5.1%</span>
          </div>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">Spending</h3>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">{formatCurrency(totalSpending)}</p>
        </motion.div>

        <motion.div 
          className="glass-card p-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
        >
          <div className="flex items-center justify-between mb-4">
            <div className={`w-12 h-12 bg-gradient-to-br ${savingsRate > 20 ? 'from-green-500 to-emerald-600' : savingsRate > 10 ? 'from-yellow-500 to-orange-600' : 'from-red-500 to-pink-600'} rounded-xl flex items-center justify-center`}>
              <Target className="w-6 h-6 text-white" />
            </div>
            <span className={`text-xs font-semibold ${savingsRate > 20 ? 'text-green-600 dark:text-green-400' : savingsRate > 10 ? 'text-yellow-600 dark:text-yellow-400' : 'text-red-600 dark:text-red-400'}`}>
              {savingsRate > 20 ? 'Excellent' : savingsRate > 10 ? 'Good' : 'Needs Work'}
            </span>
          </div>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-1">Savings Rate</h3>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">{savingsRate.toFixed(1)}%</p>
        </motion.div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Account Balances */}
        <motion.div 
          className="glass-card p-6"
          initial={{ opacity: 0, x: -20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.5 }}
        >
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-6">Account Balances</h3>
          <div className="space-y-4">
            {accounts.map((account, index) => {
              const Icon = getAccountIcon(account.type);
              const colorClass = getAccountColor(account.type);
              
              return (
                <motion.div 
                  key={account.id} 
                  className="flex items-center justify-between p-4 glass rounded-xl hover:shadow-lg transition-all duration-200"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.6 + index * 0.1 }}
                >
                  <div className="flex items-center gap-4">
                    <div className={`w-12 h-12 bg-gradient-to-br ${colorClass} rounded-xl flex items-center justify-center shadow-lg`}>
                      <Icon className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h4 className="font-semibold text-gray-900 dark:text-white">{account.name}</h4>
                      <p className="text-sm text-gray-500 dark:text-gray-400">{account.institution}</p>
                    </div>
                  </div>
                  <div className="text-right">
                    <p className={`text-lg font-bold ${account.balance >= 0 ? 'text-gray-900 dark:text-white' : 'text-red-600 dark:text-red-400'}`}>
                      {account.balance < 0 ? '-' : ''}{formatCurrency(account.balance)}
                    </p>
                    <p className="text-xs text-gray-400 capitalize">{account.type}</p>
                  </div>
                </motion.div>
              );
            })}
          </div>
        </motion.div>

        {/* Spending Categories */}
        <motion.div 
          className="glass-card p-6"
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.5 }}
        >
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-6">Spending Breakdown</h3>
          <div className="space-y-4">
            {spendingCategories.map((category, index) => (
              <motion.div 
                key={category.name} 
                className="flex items-center justify-between"
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.9 + index * 0.05 }}
              >
                <div className="flex items-center gap-3 flex-1">
                  <div className={`w-4 h-4 rounded-full ${category.color}`}></div>
                  <span className="text-gray-700 dark:text-gray-300 font-medium">{category.name}</span>
                </div>
                <div className="flex items-center gap-4">
                  <div className="w-32 bg-gray-200 dark:bg-gray-700 rounded-full h-2 overflow-hidden">
                    <motion.div 
                      className={`h-2 rounded-full ${category.color}`}
                      initial={{ width: 0 }}
                      animate={{ width: `${category.percentage}%` }}
                      transition={{ duration: 1, delay: 1 + index * 0.1, ease: "easeOut" }}
                    />
                  </div>
                  <span className="text-sm font-semibold text-gray-900 dark:text-white w-20 text-right">
                    {formatCurrency(category.amount)}
                  </span>
                  <span className="text-xs text-gray-500 w-8 text-right">
                    {category.percentage}%
                  </span>
                </div>
              </motion.div>
            ))}
          </div>
          <div className="mt-6 pt-4 border-t border-gray-200 dark:border-gray-700">
            <div className="flex justify-between items-center">
              <span className="font-semibold text-gray-900 dark:text-white">Total Monthly Spending</span>
              <span className="text-xl font-bold text-gray-900 dark:text-white">{formatCurrency(totalSpending)}</span>
            </div>
          </div>
        </motion.div>
      </div>

      {/* Virtualized Transaction List */}
      <motion.div 
        className="glass-card p-6"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.8 }}
      >
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-6">Recent Transactions</h3>
        <VirtualizedTransactionList 
          transactions={recentTransactions}
          onTransactionClick={(transaction) => console.log('Transaction clicked:', transaction)}
        />
      </motion.div>

      {/* AI Financial Insight */}
      <motion.div 
        className="bg-gradient-to-br from-green-50 to-emerald-50 dark:from-green-900/20 dark:to-emerald-900/20 border border-green-200 dark:border-green-700/50 rounded-xl p-6"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 1.2 }}
      >
        <div className="flex items-center gap-3 mb-4">
          <div className="w-10 h-10 bg-gradient-to-br from-green-500 to-emerald-600 rounded-xl flex items-center justify-center">
            <Zap className="w-5 h-5 text-white" />
          </div>
          <div>
            <h3 className="font-semibold text-green-800 dark:text-green-300">AI Financial Insight</h3>
            <p className="text-xs text-green-600 dark:text-green-400">Personalized recommendation based on your spending patterns</p>
          </div>
        </div>
        
        {!isPremiumUser ? (
          <div className="flex items-center justify-between">
            <p className="text-green-700 dark:text-green-400">
              Upgrade to Premium for personalized financial insights and spending optimization recommendations.
            </p>
            <motion.button 
              onClick={() => {}} 
              className="btn-primary text-sm"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              Upgrade
            </motion.button>
          </div>
        ) : isLoadingInsight ? (
          <SkeletonLoader count={2} />
        ) : aiInsight ? (
          <div className="text-green-800 dark:text-green-300">
            <TypewriterText text={aiInsight} speed={25} />
          </div>
        ) : null}
      </motion.div>

      {insightError && (
        <ErrorToast
          error={insightError}
          onClose={() => setInsightError(null)}
          onRetry={generateFinancialInsight}
        />
      )}
    </div>
  );
});

FinancialDashboard.displayName = 'FinancialDashboard';

export default FinancialDashboard;
