import React, { useState } from 'react';
import { X, Shield, Lock, Eye, CheckCircle, AlertTriangle, ExternalLink } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import type { Integration } from '../types';
import { ASSETS } from '../assetManifest';

interface IntegrationModalProps {
  integration: Integration | null;
  isOpen: boolean;
  onClose: () => void;
  onConnect: (integrationId: string) => void;
  isConnecting: boolean;
}

const IntegrationModal: React.FC<IntegrationModalProps> = ({
  integration,
  isOpen,
  onClose,
  onConnect,
  isConnecting,
}) => {
  const [step, setStep] = useState<'overview' | 'permissions' | 'connecting'>('overview');

  if (!integration) return null;

  const getIntegrationDetails = () => {
    switch (integration.id) {
      case 'plaid':
        return {
          logo: ASSETS.logos.plaid.path,
          description: 'Securely connect your bank accounts to track spending, income, and financial health.',
          permissions: [
            'Read account balances and transaction history',
            'Access account and routing numbers',
            'View account holder information',
          ],
          dataUsage: 'We use your financial data to provide personalized insights, spending analysis, and budget recommendations. Your data is encrypted and never shared with third parties.',
          benefits: [
            'Automatic expense categorization',
            'Real-time balance monitoring',
            'Spending pattern analysis',
            'Budget optimization suggestions',
          ],
        };
      case 'gmail':
        return {
          logo: ASSETS.logos.gmail.path,
          description: 'Connect your Gmail to monitor important emails and manage your communication efficiently.',
          permissions: [
            'Read email messages and metadata',
            'Access email labels and folders',
            'View sender and recipient information',
          ],
          dataUsage: 'We analyze your emails to identify urgent messages, track communication patterns, and provide productivity insights. We never read email content beyond metadata analysis.',
          benefits: [
            'Priority email identification',
            'Communication pattern analysis',
            'Unread message tracking',
            'Response time optimization',
          ],
        };
      case 'fitbit':
        return {
          logo: ASSETS.logos.fitbit.path,
          description: 'Sync your Fitbit data to track health metrics and receive personalized wellness insights.',
          permissions: [
            'Read activity and exercise data',
            'Access sleep and heart rate information',
            'View weight and body composition',
          ],
          dataUsage: 'Your health data helps us provide personalized wellness recommendations and track your progress toward health goals.',
          benefits: [
            'Comprehensive health tracking',
            'Sleep quality analysis',
            'Activity goal monitoring',
            'Health trend insights',
          ],
        };
      case 'twitter':
        return {
          logo: ASSETS.logos.twitter.path,
          description: 'Monitor your Twitter mentions and engagement to stay on top of your social media presence.',
          permissions: [
            'Read tweets and mentions',
            'Access follower information',
            'View engagement metrics',
          ],
          dataUsage: 'We analyze your social media activity to provide insights on engagement, mentions, and communication effectiveness.',
          benefits: [
            'Mention tracking and alerts',
            'Engagement analytics',
            'Social media presence monitoring',
            'Communication effectiveness insights',
          ],
        };
      default:
        return {
          logo: ASSETS.icons.integrationIcon.path,
          description: 'Connect this service to enhance your Aura experience.',
          permissions: ['Basic account access'],
          dataUsage: 'Data is used to provide personalized insights.',
          benefits: ['Enhanced functionality'],
        };
    }
  };

  const details = getIntegrationDetails();

  const handleConnect = () => {
    setStep('connecting');
    onConnect(integration.id);
  };

  const modalVariants = {
    hidden: { opacity: 0, scale: 0.95 },
    visible: { opacity: 1, scale: 1 },
    exit: { opacity: 0, scale: 0.95 }
  };

  const stepVariants = {
    hidden: { opacity: 0, x: 20 },
    visible: { opacity: 1, x: 0 },
    exit: { opacity: 0, x: -20 }
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div
          className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={onClose}
        >
          <motion.div
            className="bg-white dark:bg-gray-900 rounded-2xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-hidden"
            variants={modalVariants}
            initial="hidden"
            animate="visible"
            exit="exit"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Header */}
            <div className="flex items-center justify-between p-6 border-b border-gray-200 dark:border-gray-700">
              <div className="flex items-center gap-4">
                <img 
                  src={details.logo} 
                  alt={integration.name}
                  className="w-12 h-12 rounded-xl"
                />
                <div>
                  <h2 className="text-xl font-bold text-gray-900 dark:text-white">
                    Connect {integration.name}
                  </h2>
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    {step === 'overview' && 'Review integration details'}
                    {step === 'permissions' && 'Data access permissions'}
                    {step === 'connecting' && 'Establishing connection...'}
                  </p>
                </div>
              </div>
              <button
                onClick={onClose}
                className="p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors"
                disabled={isConnecting}
              >
                <X className="w-5 h-5 text-gray-500 dark:text-gray-400" />
              </button>
            </div>

            {/* Content */}
            <div className="p-6 overflow-y-auto max-h-[calc(90vh-140px)]">
              <AnimatePresence mode="wait">
                {step === 'overview' && (
                  <motion.div
                    key="overview"
                    variants={stepVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    className="space-y-6"
                  >
                    <div>
                      <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-3">
                        What this integration does
                      </h3>
                      <p className="text-gray-600 dark:text-gray-400 leading-relaxed">
                        {details.description}
                      </p>
                    </div>

                    <div>
                      <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-3">
                        Benefits you'll get
                      </h3>
                      <ul className="space-y-2">
                        {details.benefits.map((benefit, index) => (
                          <li key={index} className="flex items-center gap-3">
                            <CheckCircle className="w-5 h-5 text-green-500 flex-shrink-0" />
                            <span className="text-gray-600 dark:text-gray-400">{benefit}</span>
                          </li>
                        ))}
                      </ul>
                    </div>

                    <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-xl p-4">
                      <div className="flex items-start gap-3">
                        <Shield className="w-5 h-5 text-blue-600 dark:text-blue-400 mt-0.5 flex-shrink-0" />
                        <div>
                          <h4 className="font-semibold text-blue-800 dark:text-blue-300 mb-1">
                            Your data is secure
                          </h4>
                          <p className="text-sm text-blue-700 dark:text-blue-400">
                            We use bank-level encryption and never store your login credentials. 
                            You can disconnect at any time.
                          </p>
                        </div>
                      </div>
                    </div>
                  </motion.div>
                )}

                {step === 'permissions' && (
                  <motion.div
                    key="permissions"
                    variants={stepVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    className="space-y-6"
                  >
                    <div>
                      <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-3 flex items-center gap-2">
                        <Eye className="w-5 h-5 text-purple-500" />
                        Data Access Permissions
                      </h3>
                      <p className="text-gray-600 dark:text-gray-400 mb-4">
                        {integration.name} will be able to access the following data:
                      </p>
                      <ul className="space-y-3">
                        {details.permissions.map((permission, index) => (
                          <li key={index} className="flex items-start gap-3 p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                            <Lock className="w-4 h-4 text-gray-500 mt-0.5 flex-shrink-0" />
                            <span className="text-gray-700 dark:text-gray-300">{permission}</span>
                          </li>
                        ))}
                      </ul>
                    </div>

                    <div>
                      <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-3">
                        How we use your data
                      </h3>
                      <p className="text-gray-600 dark:text-gray-400 leading-relaxed">
                        {details.dataUsage}
                      </p>
                    </div>

                    <div className="bg-amber-50 dark:bg-amber-900/20 border border-amber-200 dark:border-amber-800 rounded-xl p-4">
                      <div className="flex items-start gap-3">
                        <AlertTriangle className="w-5 h-5 text-amber-600 dark:text-amber-400 mt-0.5 flex-shrink-0" />
                        <div>
                          <h4 className="font-semibold text-amber-800 dark:text-amber-300 mb-1">
                            Important
                          </h4>
                          <p className="text-sm text-amber-700 dark:text-amber-400">
                            By connecting this integration, you agree to share the specified data 
                            with Aura for the purposes described above.
                          </p>
                        </div>
                      </div>
                    </div>
                  </motion.div>
                )}

                {step === 'connecting' && (
                  <motion.div
                    key="connecting"
                    variants={stepVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    className="text-center py-8"
                  >
                    <motion.div
                      animate={{ rotate: 360 }}
                      transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
                      className="w-16 h-16 gradient-primary rounded-2xl flex items-center justify-center mx-auto mb-6"
                    >
                      <ExternalLink className="w-8 h-8 text-white" />
                    </motion.div>
                    <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-2">
                      Connecting to {integration.name}
                    </h3>
                    <p className="text-gray-600 dark:text-gray-400 mb-6">
                      Please complete the authentication process in the popup window.
                    </p>
                    <div className="flex items-center justify-center gap-2 text-sm text-gray-500">
                      <div className="w-2 h-2 bg-purple-500 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-purple-500 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                      <div className="w-2 h-2 bg-purple-500 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
            </div>

            {/* Footer */}
            {step !== 'connecting' && (
              <div className="flex items-center justify-between p-6 border-t border-gray-200 dark:border-gray-700">
                <div className="flex items-center gap-2 text-sm text-gray-500">
                  <Shield className="w-4 h-4" />
                  <span>256-bit SSL encryption</span>
                </div>
                <div className="flex gap-3">
                  {step === 'permissions' && (
                    <button
                      onClick={() => setStep('overview')}
                      className="btn-secondary"
                    >
                      Back
                    </button>
                  )}
                  <button
                    onClick={step === 'overview' ? () => setStep('permissions') : handleConnect}
                    className="btn-primary"
                    disabled={isConnecting}
                  >
                    {step === 'overview' ? 'Review Permissions' : 'Connect Now'}
                  </button>
                </div>
              </div>
            )}
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};

export default IntegrationModal;
