import React, { useState } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import PriorityCard from './PriorityCard';
import IntegrationCard from './IntegrationCard';
import FinancialDashboard from './FinancialDashboard';
import HealthDashboard from './HealthDashboard';
import CommunicationHub from './CommunicationHub';
import SubscriptionTiers from './SubscriptionTiers';
import PredictiveInsights from './PredictiveInsights';
import NotificationPreferences from './NotificationPreferences';
import DashboardCustomizer from './DashboardCustomizer';
import OfflineIndicator from './OfflineIndicator';
import type { Task, CalendarEvent, AIInsight } from '../types';
import { Menu, Bell, DollarSign, Heart, MessageSquare, Settings, CreditCard, LayoutDashboard, TrendingUp, Brain } from 'lucide-react';
import { useIntegrations } from '../hooks/useIntegrations';
import { useDashboardLayout } from '../hooks/useDashboardLayout';

interface CommandCenterProps {
  onInspectInsight: (insight: AIInsight) => void;
  onMenuClick: () => void;
}

const CommandCenter: React.FC<CommandCenterProps> = ({ onInspectInsight, onMenuClick }) => {
  const { useStorage, user } = useSubscribeDev();
  const { integrations } = useIntegrations();
  const { currentLayout } = useDashboardLayout();
  const [activeTab, setActiveTab] = useState<'overview' | 'financial' | 'health' | 'communication' | 'integrations' | 'subscription' | 'analytics' | 'preferences'>('overview');

  // Using useStorage to persist mock data for the user with proper default values
  const [tasks] = useStorage<Task[]>('user_tasks', [
    { id: '1', title: 'Finalize Q4 report', dueDate: new Date(Date.now() + 2 * 24 * 60 * 60 * 1000).toISOString(), priority: 'high' },
    { id: '2', title: 'Prepare for team sync', dueDate: new Date(Date.now() + 1 * 60 * 60 * 1000).toISOString(), priority: 'medium' },
    { id: '3', title: 'Book dentist appointment', dueDate: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString(), priority: 'low' },
  ]);

  const [events] = useStorage<CalendarEvent[]>('user_events', [
    { id: '1', title: 'Team Sync', startTime: '14:00', endTime: '15:00' },
    { id: '2', title: 'Project Phoenix Deep Dive', startTime: '10:00', endTime: '11:30' },
  ]);

  // Ensure arrays are never undefined
  const safeTasks = Array.isArray(tasks) ? tasks : [];
  const safeEvents = Array.isArray(events) ? events : [];

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return 'Good morning';
    if (hour < 18) return 'Good afternoon';
    return 'Good evening';
  };

  const tabs = [
    { id: 'overview', label: 'Overview', icon: LayoutDashboard },
    { id: 'analytics', label: 'Analytics', icon: TrendingUp },
    { id: 'financial', label: 'Financial', icon: DollarSign },
    { id: 'health', label: 'Health', icon: Heart },
    { id: 'communication', label: 'Communication', icon: MessageSquare },
    { id: 'integrations', label: 'Integrations', icon: Settings },
    { id: 'subscription', label: 'Subscription', icon: CreditCard },
    { id: 'preferences', label: 'Preferences', icon: Brain },
  ];

  const renderCustomDashboard = () => {
    return (
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {currentLayout.widgets.map((widget) => {
          switch (widget.type) {
            case 'ai-insights':
              return (
                <div key={widget.id} className={widget.size === 'large' ? 'lg:col-span-2' : ''}>
                  <PriorityCard tasks={safeTasks} events={safeEvents} onInspect={onInspectInsight} />
                </div>
              );
            case 'health-metrics':
              return (
                <div key={widget.id} className={widget.size === 'large' ? 'lg:col-span-2' : ''}>
                  <div className="glass-card p-6">
                    <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Health Overview</h3>
                    <HealthDashboard />
                  </div>
                </div>
              );
            case 'financial-summary':
              return (
                <div key={widget.id} className={widget.size === 'large' ? 'lg:col-span-2' : ''}>
                  <div className="glass-card p-6">
                    <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Financial Summary</h3>
                    <FinancialDashboard />
                  </div>
                </div>
              );
            case 'calendar-events':
              return (
                <div key={widget.id} className={widget.size === 'large' ? 'lg:col-span-2' : ''}>
                  <div className="glass-card p-6">
                    <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Upcoming Events</h3>
                    <div className="space-y-3">
                      {safeEvents.map((event) => (
                        <div key={event.id} className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                          <div>
                            <h4 className="font-medium text-gray-900 dark:text-white">{event.title}</h4>
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              {event.startTime} - {event.endTime}
                            </p>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              );
            case 'tasks':
              return (
                <div key={widget.id} className={widget.size === 'large' ? 'lg:col-span-2' : ''}>
                  <div className="glass-card p-6">
                    <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Tasks</h3>
                    <div className="space-y-3">
                      {safeTasks.map((task) => (
                        <div key={task.id} className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                          <div>
                            <h4 className="font-medium text-gray-900 dark:text-white">{task.title}</h4>
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              Due: {new Date(task.dueDate).toLocaleDateString()}
                            </p>
                          </div>
                          <span className={`px-2 py-1 rounded text-xs font-medium ${
                            task.priority === 'high' ? 'bg-red-100 text-red-700' :
                            task.priority === 'medium' ? 'bg-yellow-100 text-yellow-700' :
                            'bg-green-100 text-green-700'
                          }`}>
                            {task.priority}
                          </span>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              );
            case 'communication':
              return (
                <div key={widget.id} className={widget.size === 'large' ? 'lg:col-span-2' : ''}>
                  <div className="glass-card p-6">
                    <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Communication</h3>
                    <CommunicationHub />
                  </div>
                </div>
              );
            default:
              return null;
          }
        })}
      </div>
    );
  };

  return (
    <div className="flex-1 p-4 sm:p-8 overflow-y-auto">
      <OfflineIndicator />
      
      <header className="mb-8 flex items-center justify-between">
        <div className="flex items-center">
          <button 
            onClick={onMenuClick} 
            className="md:hidden mr-4 text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white transition-colors p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800"
          >
            <Menu className="w-6 h-6" />
          </button>
          <div>
            <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
              {getGreeting()}{user ? `, ${user.email.split('@')[0]}` : ''}
            </h1>
            <p className="text-gray-500 dark:text-gray-400 mt-1">Your unified overview. Here's what matters now.</p>
          </div>
        </div>
        <button className="p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white transition-all duration-200">
          <Bell className="w-6 h-6" />
        </button>
      </header>

      {/* Navigation Tabs */}
      <div className="mb-8">
        <div className="border-b border-gray-200 dark:border-gray-700">
          <nav className="-mb-px flex space-x-8 overflow-x-auto">
            {tabs.map((tab) => {
              const Icon = tab.icon;
              return (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id as any)}
                  className={`whitespace-nowrap py-2 px-1 border-b-2 font-medium text-sm flex items-center gap-2 transition-colors ${
                    activeTab === tab.id
                      ? 'border-purple-500 text-purple-600 dark:text-purple-400'
                      : 'border-transparent text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300 hover:border-gray-300 dark:hover:border-gray-600'
                  }`}
                >
                  <Icon className="w-4 h-4" />
                  {tab.label}
                </button>
              );
            })}
          </nav>
        </div>
      </div>

      {/* Tab Content */}
      <div className="space-y-8">
        {activeTab === 'overview' && (
          <div className="transition-all duration-500 ease-out">
            {renderCustomDashboard()}
          </div>
        )}

        {activeTab === 'analytics' && <PredictiveInsights />}
        {activeTab === 'financial' && <FinancialDashboard />}
        {activeTab === 'health' && <HealthDashboard />}
        {activeTab === 'communication' && <CommunicationHub />}
        {activeTab === 'preferences' && <NotificationPreferences />}
        
        {activeTab === 'integrations' && (
          <div className="space-y-6">
            <div>
              <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">Integrations</h2>
              <p className="text-gray-600 dark:text-gray-400">Connect your accounts to unlock powerful AI insights</p>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {integrations.map((integration) => (
                <IntegrationCard
                  key={integration.id}
                  integration={integration}
                />
              ))}
            </div>
          </div>
        )}

        {activeTab === 'subscription' && <SubscriptionTiers />}
      </div>

      <DashboardCustomizer />
    </div>
  );
};

export default CommandCenter;
