import React, { useState, useRef, useEffect } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { MessageCircle, Send, X, Loader, Minimize2, Heart, Sparkles, Mic, MoreVertical, Copy, RefreshCw, Zap } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { z } from 'zod';
import TypewriterText from './TypewriterText';

interface Message {
  id: string;
  content: string;
  isUser: boolean;
  timestamp: Date;
  liked?: boolean;
  emoji?: string;
  isTyping?: boolean;
}

interface QuickAction {
  id: string;
  label: string;
  prompt: string;
  icon: string;
}

// Zod schema for chat response validation
const ChatResponseSchema = z.object({
  content: z.string(),
  emoji: z.string().optional()
});

const FloatingChat: React.FC = () => {
  const { client, subscriptionStatus, usage } = useSubscribeDev();
  const [isOpen, setIsOpen] = useState(false);
  const [isMinimized, setIsMinimized] = useState(false);
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isTyping, setIsTyping] = useState(false);
  const [showQuickActions, setShowQuickActions] = useState(true);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  const isPremiumUser = subscriptionStatus?.hasActiveSubscription;

  const quickActions: QuickAction[] = [
    { id: 'priorities', label: 'What should I focus on?', prompt: 'What should I prioritize today based on my tasks and calendar?', icon: '🎯' },
    { id: 'financial', label: 'Financial advice', prompt: 'Give me a quick financial tip based on my spending patterns', icon: '💰' },
    { id: 'health', label: 'Health check', prompt: 'How am I doing with my health and wellness goals?', icon: '🏃‍♂️' },
    { id: 'productivity', label: 'Productivity boost', prompt: 'Give me a productivity tip to improve my day', icon: '⚡' }
  ];

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  useEffect(() => {
    if (isOpen && inputRef.current) {
      inputRef.current.focus();
    }
  }, [isOpen]);

  const getRandomEmoji = () => {
    const emojis = ['🤔', '💭', '✨', '🎯', '💡', '🚀', '🌟', '💪', '🎉', '👍'];
    return emojis[Math.floor(Math.random() * emojis.length)];
  };

  const sendMessage = async (messageContent?: string) => {
    const content = messageContent || inputValue.trim();
    if (!content || !client || isLoading) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      content,
      isUser: true,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);
    setIsTyping(true);
    setShowQuickActions(false);

    try {
      const systemPrompt = `You are Aura, a helpful and encouraging AI assistant. You have access to the user's tasks, calendar, financial data, and health metrics. 

Key personality traits:
- Friendly and supportive, but not overly enthusiastic
- Concise and actionable - keep responses under 150 words unless asked for detail
- Use emojis sparingly (1-2 per message max)
- Focus on practical advice and insights
- Reference specific data when relevant
- Be encouraging but realistic

Current context:
- User has an AI-powered personal assistant app
- They can track tasks, calendar, finances, and health
- They're looking for productivity and life optimization advice

Respond naturally and helpfully to: "${content}"`;

      const response = await client.run('openai/gpt-4o', {
        input: {
          messages: [
            { role: 'system', content: systemPrompt },
            { role: 'user', content }
          ]
        }
      });

      setIsTyping(false);

      const aiMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: response.output[0] as string,
        isUser: false,
        timestamp: new Date(),
        emoji: getRandomEmoji()
      };

      setMessages(prev => [...prev, aiMessage]);
    } catch (error: any) {
      setIsTyping(false);
      let errorMessage = 'Oops! Something went wrong. Let\'s try that again! 😅';
      
      if (error.type === 'insufficient_credits') {
        errorMessage = 'You\'re out of credits! Upgrade to Premium to continue chatting with me. 💎';
      } else if (error.type === 'rate_limit_exceeded') {
        errorMessage = `I need a quick break! Please wait ${Math.ceil(error.retryAfter / 1000)} seconds before trying again. ⏰`;
      }

      const errorMsg: Message = {
        id: (Date.now() + 1).toString(),
        content: errorMessage,
        isUser: false,
        timestamp: new Date(),
        emoji: '😅'
      };
      setMessages(prev => [...prev, errorMsg]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const toggleLike = (messageId: string) => {
    setMessages(prev => prev.map(msg => 
      msg.id === messageId ? { ...msg, liked: !msg.liked } : msg
    ));
  };

  const copyMessage = (content: string) => {
    navigator.clipboard.writeText(content);
  };

  const clearChat = () => {
    setMessages([]);
    setShowQuickActions(true);
  };

  const chatVariants = {
    hidden: { scale: 0, opacity: 0, originX: 1, originY: 1 },
    visible: { scale: 1, opacity: 1, originX: 1, originY: 1 }
  };

  const messageVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 }
  };

  if (!isOpen) {
    return (
      <motion.button
        onClick={() => setIsOpen(true)}
        className="fixed bottom-6 right-6 w-16 h-16 gradient-primary text-white rounded-2xl shadow-2xl glow z-50 group"
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
        variants={chatVariants}
        initial="hidden"
        animate="visible"
        transition={{ type: "spring", stiffness: 500, damping: 30 }}
      >
        <MessageCircle className="w-7 h-7 mx-auto group-hover:scale-110 transition-transform duration-200" />
        <motion.div
          className="absolute -top-2 -right-2 w-6 h-6 bg-gradient-to-r from-pink-500 to-red-500 rounded-full flex items-center justify-center"
          animate={{ scale: [1, 1.2, 1] }}
          transition={{ duration: 2, repeat: Infinity }}
        >
          <Sparkles className="w-3 h-3 text-white" />
        </motion.div>
        
        {/* Unread indicator */}
        {messages.length > 0 && (
          <motion.div
            className="absolute -top-1 -left-1 w-4 h-4 bg-red-500 rounded-full text-xs text-white flex items-center justify-center font-bold"
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ type: "spring", stiffness: 500, damping: 15 }}
          >
            {messages.filter(m => !m.isUser).length}
          </motion.div>
        )}
      </motion.button>
    );
  }

  return (
    <motion.div 
      className={`fixed bottom-6 right-6 w-96 glass-card z-50 transition-all duration-300 flex flex-col ${isMinimized ? 'h-16' : 'h-[36rem]'}`}
      variants={chatVariants}
      initial="hidden"
      animate="visible"
      transition={{ type: "spring", stiffness: 500, damping: 30 }}
    >
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b border-gray-200 dark:border-gray-700 flex-shrink-0">
        <div className="flex items-center gap-3">
          <div className="w-8 h-8 gradient-primary rounded-xl flex items-center justify-center">
            <MessageCircle className="w-4 h-4 text-white" />
          </div>
          <div>
            <span className="font-bold text-gradient">Chat with Aura</span>
            {isTyping && (
              <motion.p 
                className="text-xs text-gray-500 flex items-center gap-1"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
              >
                <span className="w-1 h-1 bg-purple-500 rounded-full animate-pulse"></span>
                <span className="w-1 h-1 bg-purple-500 rounded-full animate-pulse" style={{ animationDelay: '0.2s' }}></span>
                <span className="w-1 h-1 bg-purple-500 rounded-full animate-pulse" style={{ animationDelay: '0.4s' }}></span>
                Aura is thinking...
              </motion.p>
            )}
          </div>
        </div>
        <div className="flex items-center gap-2">
          <motion.button
            onClick={() => setIsMinimized(!isMinimized)}
            className="btn-secondary p-2"
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          >
            <Minimize2 className="w-4 h-4" />
          </motion.button>
          <div className="relative">
            <motion.button
              className="btn-secondary p-2"
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
            >
              <MoreVertical className="w-4 h-4" />
            </motion.button>
          </div>
          <motion.button
            onClick={() => setIsOpen(false)}
            className="btn-secondary p-2"
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          >
            <X className="w-4 h-4" />
          </motion.button>
        </div>
      </div>

      {!isMinimized && (
        <>
          {/* Messages */}
          <div className="flex-1 p-4 overflow-y-auto space-y-4">
            <AnimatePresence>
              {messages.length === 0 && showQuickActions && (
                <motion.div 
                  className="space-y-4"
                  variants={messageVariants}
                  initial="hidden"
                  animate="visible"
                  transition={{ type: "spring", stiffness: 500, damping: 30 }}
                >
                  <div className="text-center text-gray-500 dark:text-gray-400 text-sm">
                    {isPremiumUser ? 
                      "Hi! I'm Aura ✨ How can I help you today?" : 
                      "Upgrade to Premium to chat with Aura 🚀"
                    }
                  </div>
                  
                  {isPremiumUser && (
                    <div className="grid grid-cols-2 gap-2">
                      {quickActions.map((action) => (
                        <motion.button
                          key={action.id}
                          onClick={() => sendMessage(action.prompt)}
                          className="p-3 glass rounded-lg text-left hover:bg-gray-50 dark:hover:bg-gray-800/50 transition-colors group"
                          whileHover={{ scale: 1.02 }}
                          whileTap={{ scale: 0.98 }}
                        >
                          <div className="text-lg mb-1">{action.icon}</div>
                          <div className="text-xs text-gray-600 dark:text-gray-400 group-hover:text-gray-900 dark:group-hover:text-gray-200">
                            {action.label}
                          </div>
                        </motion.button>
                      ))}
                    </div>
                  )}
                </motion.div>
              )}
              
              {messages.map((message) => (
                <motion.div
                  key={message.id}
                  className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}
                  variants={messageVariants}
                  initial="hidden"
                  animate="visible"
                  transition={{ type: "spring", stiffness: 500, damping: 30 }}
                  layout
                >
                  <div className={`max-w-xs group ${message.isUser ? 'ml-8' : 'mr-8'}`}>
                    <div
                      className={`px-4 py-3 rounded-2xl text-sm ${
                        message.isUser
                          ? 'gradient-primary text-white'
                          : 'glass border border-gray-200 dark:border-gray-700 text-gray-900 dark:text-white'
                      }`}
                    >
                      {message.isUser ? (
                        message.content
                      ) : (
                        <div className="flex items-start gap-2">
                          {message.emoji && (
                            <span className="text-lg flex-shrink-0">{message.emoji}</span>
                          )}
                          <div className="flex-1">
                            <TypewriterText text={message.content} speed={20} />
                          </div>
                        </div>
                      )}
                    </div>
                    
                    {!message.isUser && (
                      <div className="flex items-center gap-2 mt-2 opacity-0 group-hover:opacity-100 transition-opacity">
                        <motion.button
                          onClick={() => toggleLike(message.id)}
                          className={`p-1 rounded-full transition-colors ${
                            message.liked 
                              ? 'text-red-500 bg-red-50 dark:bg-red-900/20' 
                              : 'text-gray-400 hover:text-red-500 hover:bg-red-50 dark:hover:bg-red-900/20'
                          }`}
                          whileHover={{ scale: 1.2 }}
                          whileTap={{ scale: 0.8 }}
                        >
                          <Heart className={`w-3 h-3 ${message.liked ? 'fill-current' : ''}`} />
                        </motion.button>
                        
                        <motion.button
                          onClick={() => copyMessage(message.content)}
                          className="p-1 rounded-full text-gray-400 hover:text-gray-600 dark:hover:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-800/50 transition-colors"
                          whileHover={{ scale: 1.2 }}
                          whileTap={{ scale: 0.8 }}
                        >
                          <Copy className="w-3 h-3" />
                        </motion.button>
                        
                        <span className="text-xs text-gray-400">
                          {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                        </span>
                      </div>
                    )}
                  </div>
                </motion.div>
              ))}
              
              {isLoading && !isTyping && (
                <motion.div
                  className="flex justify-start"
                  variants={messageVariants}
                  initial="hidden"
                  animate="visible"
                  transition={{ type: "spring", stiffness: 500, damping: 30 }}
                >
                  <div className="glass px-4 py-3 rounded-2xl flex items-center gap-2 border border-gray-200 dark:border-gray-700">
                    <motion.div
                      animate={{ rotate: 360 }}
                      transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                    >
                      <Loader className="w-4 h-4 text-purple-500" />
                    </motion.div>
                    <span className="text-sm text-gray-500">Thinking...</span>
                  </div>
                </motion.div>
              )}
            </AnimatePresence>
            <div ref={messagesEndRef} />
          </div>

          {/* Usage indicator */}
          {usage && isPremiumUser && (
            <div className="px-4 py-2 border-t border-gray-200 dark:border-gray-700 bg-gray-50/50 dark:bg-gray-800/50">
              <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400">
                <div className="flex items-center gap-1">
                  <Zap className="w-3 h-3" />
                  <span>Credits: {usage.remainingCredits.toLocaleString()}</span>
                </div>
                {messages.length > 0 && (
                  <button 
                    onClick={clearChat}
                    className="flex items-center gap-1 hover:text-gray-700 dark:hover:text-gray-300 transition-colors"
                  >
                    <RefreshCw className="w-3 h-3" />
                    Clear
                  </button>
                )}
              </div>
            </div>
          )}

          {/* Input */}
          <div className="p-4 border-t border-gray-200 dark:border-gray-700 flex-shrink-0">
            <div className="flex gap-2">
              <motion.button
                disabled={!isPremiumUser}
                title="Voice input (coming soon)"
                className="px-4 py-3 glass border border-gray-300 dark:border-gray-600 rounded-xl transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed text-gray-500 dark:text-gray-400 hover:text-purple-500 dark:hover:text-purple-400"
                whileHover={{ scale: isPremiumUser ? 1.05 : 1 }}
                whileTap={{ scale: isPremiumUser ? 0.95 : 1 }}
              >
                <Mic className="w-4 h-4" />
              </motion.button>
              
              <input
                ref={inputRef}
                type="text"
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder={isPremiumUser ? "Ask Aura anything... ✨" : "Upgrade to chat"}
                disabled={!isPremiumUser || isLoading}
                className="flex-1 px-4 py-3 glass border border-gray-300 dark:border-gray-600 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent text-gray-900 dark:text-white placeholder-gray-500 dark:placeholder-gray-400 disabled:opacity-50 transition-all duration-200"
              />
              
              <motion.button
                onClick={() => sendMessage()}
                disabled={!inputValue.trim() || !isPremiumUser || isLoading}
                className="px-4 py-3 gradient-primary text-white rounded-xl transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
                whileHover={{ scale: isPremiumUser && inputValue.trim() ? 1.05 : 1 }}
                whileTap={{ scale: isPremiumUser && inputValue.trim() ? 0.95 : 1 }}
              >
                <Send className="w-4 h-4" />
              </motion.button>
            </div>
          </div>
        </>
      )}
    </motion.div>
  );
};

export default FloatingChat;
