import React from 'react';
import { TrendingUp, Mail, Activity, MessageSquare, Calendar, DollarSign } from 'lucide-react';
import { motion } from 'framer-motion';
import type { Integration } from '../types';

interface DataPreviewProps {
  integration: Integration;
}

const DataPreview: React.FC<DataPreviewProps> = ({ integration }) => {
  if (!integration.connected || !integration.dataPreview) {
    return null;
  }

  const getPreviewContent = () => {
    switch (integration.id) {
      case 'plaid':
        return (
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Connected Accounts</span>
              <span className="font-semibold text-gray-900 dark:text-white">
                {integration.dataPreview.accounts}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Total Balance</span>
              <span className="font-semibold text-green-600 dark:text-green-400">
                {integration.dataPreview.totalBalance}
              </span>
            </div>
            <div className="pt-2 border-t border-gray-200 dark:border-gray-700">
              <span className="text-xs text-gray-500 dark:text-gray-400">Latest Transaction</span>
              <p className="text-sm text-gray-700 dark:text-gray-300 mt-1">
                {integration.dataPreview.lastTransaction}
              </p>
            </div>
          </div>
        );

      case 'gmail':
        return (
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Unread Emails</span>
              <span className="font-semibold text-orange-600 dark:text-orange-400">
                {integration.dataPreview.unreadEmails}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Urgent</span>
              <span className="font-semibold text-red-600 dark:text-red-400">
                {integration.dataPreview.urgentEmails}
              </span>
            </div>
            <div className="pt-2 border-t border-gray-200 dark:border-gray-700">
              <span className="text-xs text-gray-500 dark:text-gray-400">Latest Email</span>
              <p className="text-sm text-gray-700 dark:text-gray-300 mt-1">
                {integration.dataPreview.lastEmail}
              </p>
            </div>
          </div>
        );

      case 'fitbit':
        return (
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Today's Steps</span>
              <span className="font-semibold text-blue-600 dark:text-blue-400">
                {integration.dataPreview.todaySteps}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Last Night's Sleep</span>
              <span className="font-semibold text-purple-600 dark:text-purple-400">
                {integration.dataPreview.lastNightSleep}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Resting Heart Rate</span>
              <span className="font-semibold text-red-600 dark:text-red-400">
                {integration.dataPreview.heartRate}
              </span>
            </div>
          </div>
        );

      case 'twitter':
        return (
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Recent Mentions</span>
              <span className="font-semibold text-blue-600 dark:text-blue-400">
                {integration.dataPreview.mentions}
              </span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600 dark:text-gray-400">Total Engagement</span>
              <span className="font-semibold text-green-600 dark:text-green-400">
                {integration.dataPreview.engagement}
              </span>
            </div>
            <div className="pt-2 border-t border-gray-200 dark:border-gray-700">
              <span className="text-xs text-gray-500 dark:text-gray-400">Latest Mention</span>
              <p className="text-sm text-gray-700 dark:text-gray-300 mt-1">
                {integration.dataPreview.lastMention}
              </p>
            </div>
          </div>
        );

      default:
        return (
          <div className="text-center py-4">
            <span className="text-sm text-gray-500 dark:text-gray-400">
              Data preview not available
            </span>
          </div>
        );
    }
  };

  const getIcon = () => {
    switch (integration.id) {
      case 'plaid': return DollarSign;
      case 'gmail': return Mail;
      case 'fitbit': return Activity;
      case 'twitter': return MessageSquare;
      default: return TrendingUp;
    }
  };

  const Icon = getIcon();

  return (
    <motion.div
      className="mt-4 p-4 bg-gray-50 dark:bg-gray-800/50 rounded-xl border border-gray-200 dark:border-gray-700"
      initial={{ opacity: 0, height: 0 }}
      animate={{ opacity: 1, height: 'auto' }}
      transition={{ duration: 0.3 }}
    >
      <div className="flex items-center gap-2 mb-3">
        <Icon className="w-4 h-4 text-purple-500" />
        <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">
          Live Data Preview
        </span>
        <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
      </div>
      {getPreviewContent()}
      <div className="mt-3 pt-3 border-t border-gray-200 dark:border-gray-700">
        <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400">
          <span>Last synced</span>
          <span>{integration.lastSync?.toLocaleTimeString() || 'Just now'}</span>
        </div>
      </div>
    </motion.div>
  );
};

export default DataPreview;
