import React from 'react';
import { motion } from 'framer-motion';
import { Grid, Target, Heart, DollarSign, Calendar, CheckSquare, MessageSquare } from 'lucide-react';
import type { DashboardWidget } from '../types';

interface DragPreviewProps {
  widget: DashboardWidget;
  isDragging: boolean;
  position: { x: number; y: number };
}

const DragPreview: React.FC<DragPreviewProps> = ({ widget, isDragging, position }) => {
  const getWidgetIcon = (type: string) => {
    switch (type) {
      case 'ai-insights': return Target;
      case 'health-metrics': return Heart;
      case 'financial-summary': return DollarSign;
      case 'calendar-events': return Calendar;
      case 'tasks': return CheckSquare;
      case 'communication': return MessageSquare;
      default: return Grid;
    }
  };

  const getWidgetColor = (type: string) => {
    switch (type) {
      case 'ai-insights': return 'from-purple-500 to-indigo-600';
      case 'health-metrics': return 'from-red-500 to-pink-600';
      case 'financial-summary': return 'from-green-500 to-emerald-600';
      case 'calendar-events': return 'from-blue-500 to-cyan-600';
      case 'tasks': return 'from-orange-500 to-yellow-600';
      case 'communication': return 'from-indigo-500 to-purple-600';
      default: return 'from-gray-500 to-gray-600';
    }
  };

  const getSizeClass = (size: string) => {
    switch (size) {
      case 'large': return 'w-80 h-48';
      case 'medium': return 'w-64 h-32';
      case 'small': return 'w-48 h-24';
      default: return 'w-64 h-32';
    }
  };

  const Icon = getWidgetIcon(widget.type);
  const colorClass = getWidgetColor(widget.type);
  const sizeClass = getSizeClass(widget.size);

  if (!isDragging) return null;

  return (
    <motion.div
      className={`fixed pointer-events-none z-50 ${sizeClass}`}
      style={{
        left: position.x - 128, // Center the preview on cursor
        top: position.y - 64,
      }}
      initial={{ scale: 0.8, opacity: 0 }}
      animate={{ scale: 1, opacity: 0.9 }}
      transition={{ type: 'spring', stiffness: 500, damping: 30 }}
    >
      <div className="glass-card p-6 border-2 border-purple-500 border-dashed shadow-2xl">
        <div className="flex items-center gap-3 mb-4">
          <div className={`w-10 h-10 rounded-lg bg-gradient-to-br ${colorClass} flex items-center justify-center`}>
            <Icon className="w-5 h-5 text-white" />
          </div>
          <div>
            <h4 className="font-semibold text-gray-900 dark:text-white">{widget.title}</h4>
            <span className="text-xs px-2 py-1 bg-purple-100 dark:bg-purple-900/30 text-purple-600 dark:text-purple-400 rounded">
              {widget.size}
            </span>
          </div>
        </div>
        
        <div className="h-16 bg-gray-100 dark:bg-gray-800 rounded-lg flex items-center justify-center">
          <span className="text-gray-500 dark:text-gray-400 text-sm">
            {widget.type.replace('-', ' ').toUpperCase()} Preview
          </span>
        </div>
      </div>
    </motion.div>
  );
};

export default React.memo(DragPreview);
